/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Navbar, Nav, Container, Row, Col, Form } from "react-bootstrap";
import { PowerSettingsNew, AccountCircle } from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";
import UserEditor from "../userEditor";
import IdleTimer from "../idleTimer";
import EditModeSwitch from "../editModeSwitch";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "MainMenu" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    settings,
    sessionTimeout,
    // accountNumber,
    userData,
    signedIn,
    contactMessageEditMode,
    getFileLinkFromURI,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const {
    getTopics,
    getTextsList,
    getUserPermissionStatus,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  // console.log(
  //   "window.location.hostname:",
  //   window.location.hostname,
  //   window.location.port
  // );

  /**
    * Functions
    ============================================================================ */
  const toggleMode = (isEditMode) => {
    getTopics({ newPage: 1, includeEmpty: isEditMode });
    getTextsList({ newPage: 1, showAll: isEditMode });
  };

  const getJSX = () => {
    return (
      <Container
        fluid
        style={{
          paddingRight: "0",
          paddingLeft: "0",
        }}
      >
        <Row>
          {/*
           * User Menu
           */}
          <Col>
            <Navbar
              // className="card-header"
              // className="navbar-trans"
              // bg="light"
              // variant="light"
              style={{
                borderRadius: 0,
                // background: "transparent !important",
                // color: "#f7f7f7",
                backgroundColor: "var(--light)", // e8f6f3
                boxShadow: "none",
                // whiteSpace: "nowrap",
              }}
            >
              {/**
               * Brand
               */}
              <Navbar.Brand href="/">
                <img
                  src={`${window.location.protocol}//${window.location.hostname}:${window.location.port}/gfx/zlogo_light.png`}
                  alt=""
                  width="auto"
                  height="30"
                  className="d-inline-block align-top"
                />
              </Navbar.Brand>

              {/**
               * User editor
               */}
              <Nav className="mr-auto">
                {signedIn ? (
                  <>
                    {window.location.pathname !== "/auth" ? (
                      <Nav.Link eventKey="profile" href="/profile">
                        <UserEditor {...args} />
                      </Nav.Link>
                    ) : (
                      <Nav.Item>
                        <UserEditor {...args} />
                      </Nav.Item>
                    )}
                  </>
                ) : (
                  <>
                    {false ? (
                      <Nav.Item>
                        <strong>{t("Local Currency")}</strong>
                      </Nav.Item>
                    ) : (
                      <></>
                    )}
                  </>
                )}
              </Nav>

              {/**
               * Register
               */}
              {!signedIn && window.location.pathname !== "/sign-up" && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-up">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <AccountCircle
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Register")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Sign in
               */}
              {!signedIn && window.location.pathname !== "/sign-in" && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-in">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <PowerSettingsNew
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Sign in")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Sign out
               */}
              {signedIn && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-out">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <PowerSettingsNew
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Sign out")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Idle timer
               */}
              {signedIn && (
                <Nav>
                  <Nav.Item>
                    <IdleTimer timeout={sessionTimeout} {...args} />
                  </Nav.Item>
                </Nav>
              )}
            </Navbar>
          </Col>
        </Row>

        {/*
         * Account Menu
         */}
        {signedIn && window.location.pathname !== "/auth" && (
          <Row>
            <Col>
              <Navbar
                collapseOnSelect
                expand="sm"
                variant="dark"
                style={{
                  borderRadius: 0,
                  background: "transparent !important",
                  backgroundColor: "var(--dark)",
                  boxShadow: "none",
                }}
              >
                <Nav>
                  {/**
                   * Account
                   */}
                  <Nav.Link
                    className="nav-link-2"
                    eventKey="account"
                    href="/account"
                    style={{
                      fontWeight:
                        window.location.pathname === "/account"
                          ? "bold"
                          : "normal",
                    }}
                  >
                    {t("Account")}
                  </Nav.Link>
                </Nav>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                  <Nav className="mr-auto">
                    {userData.uid !== null && (
                      <>
                        {/**
                         * Transfer
                         */}
                        <Nav.Link
                          className="nav-link-2"
                          eventKey="transfer"
                          href="/transfer"
                          style={{
                            fontWeight:
                              window.location.pathname === "/transfer"
                                ? "bold"
                                : "normal",
                          }}
                        >
                          {t("Transfer")}
                        </Nav.Link>

                        {/**
                         * Business
                         */}
                        <Nav.Link
                          className="nav-link-2"
                          eventKey="business"
                          href="/business"
                          style={{
                            fontWeight:
                              window.location.pathname === "/business"
                                ? "bold"
                                : "normal",
                          }}
                        >
                          {t("Business")}
                        </Nav.Link>

                        {/**
                         * Currency
                         */}
                        {settings !== null &&
                          typeof settings["currencies"] !== "undefined" &&
                          (settings["currencies"] === null ||
                            settings["currencies"].length === 0 ||
                            settings["currencies"].length > 1) && (
                            <Nav.Link
                              className="nav-link-2"
                              eventKey="currency"
                              href="/currency"
                              style={{
                                fontWeight:
                                  window.location.pathname === "/currency"
                                    ? "bold"
                                    : "normal",
                              }}
                            >
                              {t("Currency")}
                            </Nav.Link>
                          )}
                      </>
                    )}
                  </Nav>
                  {getUserPermissionStatus(10) ? (
                    <Form inline>
                      <EditModeSwitch
                        label={contactMessageEditMode ? t("Edit") : t("View")}
                        onChange={toggleMode}
                        {...args}
                      />
                    </Form>
                  ) : (
                    <></>
                  )}
                </Navbar.Collapse>
              </Navbar>
            </Col>
          </Row>
        )}
      </Container>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
