/**
 * User CSS
 ============================================================================ */
import "./RichText.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form } from "react-bootstrap";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import { useEventListener } from "../utils/useEventListener";
import Toolbar from "./RichTextToolbar";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "RichText",
    name: "RichText",
    value: "",
    onChange: () => {},
    onBlur: () => {},
    isInvalid: false,
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const refRichText = React.createRef();

  useEventListener(
    (event) => {
      if (event.target.getAttribute("name") === "richText") {
        props.onChange(event.target.innerHTML);
      }
    },
    ["input"]
  );

  React.useEffect(() => {
    refRichText.current.innerHTML = props.value;

    // eslint-disable-next-line
  }, [props.value]);

  /**
   * Functions
   ============================================================================ */

  const onFormat = () => {
    refRichText.current.focus();
  };

  const CustomRichText = React.forwardRef(
    ({ children, onChange, value }, ref) => {
      // console.log("value:", props.value);
      return (
        <>
          <Toolbar onFormat={onFormat} {...args} />
          <div
            ref={refRichText}
            className="richtext"
            contentEditable={true}
            name="richText"
            data-placeholder="Edit me"
            suppressContentEditableWarning={true}
            onChange={onChange}
            onBlur={() => {}}
          ></div>
        </>
      );
    }
  );

  /**
   * Return
   ============================================================================ */
  return (
    <Form.Control
      as={CustomRichText}
      placeholder={""}
      name={props.name}
      value={props.value}
      onChange={props.onChange}
      isInvalid={props.isInvalid}
    />
  );
};
