/**
 * System imports
 ============================================================================ */
import React, { lazy } from "react";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import ErrorBoundary from "../errorBoundary";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  // if (true) return <></>;
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "FileLoader",
    request: "get article",
    path1: "components",
    path2: "locale",
    path3: "topics",
    file: null,
    defaultFile: "common-questions.js",
    useLanguage: true,
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getFileNameFromURI, getFileLinkFromURI } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { defaultLanguage } = props.barrelProps;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Effects
   ============================================================================ */

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    let fileName = props.file !== null ? props.file : getFileNameFromURI();

    // console.log(
    //   "fileName:",
    //   fileName,
    //   props.path1,
    //   props.path2,
    //   props.path3,
    //   props.defaultFile
    // );

    let ExternalFile = null;

    if (props.useLanguage) {
      ExternalFile =
        fileName !== null
          ? lazy(() =>
              import(
                `../../${props.path1}/${props.path2}/${language}/${props.path3}/${fileName}`
              ).catch(() => {
                return import(
                  `../../${props.path1}/${props.path2}/${language}/${props.path3}/${props.defaultFile}`
                ).catch(() => {
                  return import(
                    `../../${props.path1}/${props.path2}/${defaultLanguage}/${props.path3}/${fileName}`
                  ).catch(() => {
                    return import(
                      `../../${props.path1}/${props.path2}/${defaultLanguage}/${props.path3}/${props.defaultFile}`
                    );
                  });
                });
              })
            )
          : null;
    } else {
      ExternalFile =
        fileName !== null
          ? lazy(() =>
              import(`../../${props.path1}/${props.path2}/${fileName}`).catch(
                () => {
                  return import(
                    `../../${props.path1}/${props.path2}/${props.defaultFile}`
                  );
                }
              )
            )
          : null;
    }

    return (
      <>
        {ExternalFile !== null && (
          <ErrorBoundary>
            <ExternalFile {...args} />
          </ErrorBoundary>
        )}
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
