/**
 * System imports
 ============================================================================ */
import React from "react";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "InjectChildren",
    inject: "top",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Return
   ============================================================================ */
  return (
    <>
      {React.Children.map(args.children, (child) => {
        if (typeof child !== "object") {
          return child;
        } else {
          if (
            typeof child.props.inject === "undefined" ||
            child.props.inject === props.inject
          ) {
            return React.cloneElement(child, {
              ...args,
              styles: null,
              key: child.key,
            });
          }
        }
      })}
    </>
  );
};
