/**
 * CSS
 ============================================================================ */
import "./TableViewer.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Form, Table } from "react-bootstrap";
import { Edit as EditIcon } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
// import ListGroupItem from "../listGroupItem";
import Pagination from "../pagination";
import Sortable from "../sortable";
import SubmitButton from "../submitButton";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "TableViewer",
    request: null,
    exclude: "",
    countRows: null,
    rowsPerPage: null,
    activePage: null,
    striped: true,
    borderless: false,
    hover: true,
    showDate: true,
    datePosition: "right",
    dateFormat: {
      month: "numeric",
      day: "numeric",
      year: "numeric",
    },
    showAppetizer: false,
    thead: true,
    fileList: null,
    filesPath: "/",
    fileIdField: "fileid",
    fileTitle: "Title",
    fileDate: "Created",
    setFileList: () => {},
    onPageClick: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { contactMessageEditMode } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const {
    formatDate,
    sendRequest,
    getUserPermissionStatus,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Refs
   ============================================================================ */
  const refEdit = [];
  const refTitles = [];

  /**
   * Functions
   ============================================================================ */
  const onSubmit = (files) => {
    if (typeof files === "undefined") files = props.fileList;

    const filesUpdated = updateFiles(files);

    props.setFileList(JSON.stringify(filesUpdated));

    const data = { files: filesUpdated };

    return sendRequest({ req: props.request, data });
  };

  const updateFiles = (files) => {
    const filesUpdated = [];

    files.map((fileString, index) => {
      // const record = JSON.parse(fileString);
      const record = fileString;
      const { id, title } = record;
      const refID = id;

      if (refTitles[refID].current !== null) {
        record.title = refTitles[refID].current.value;
      }

      filesUpdated.push(record);
    });
    // console.log(topicsUpdated);
    return filesUpdated;
  };

  const rowClick = (link) => {
    window.location = `${props.filesPath}/${link}`;
  };

  const ListViewerViewMode = () => {
    return (
      <Table
        responsive
        striped={props.striped}
        borderless={props.borderless}
        hover={props.hover}
      >
        {props.thead && (
          <thead style={{}}>
            <tr>
              <th
                className="table-viewer-heading"
                style={{
                  textAlign: "left",
                  border: "0",
                  whiteSpace: "nowrap",
                  // marginLeft: "0",
                  // paddingLeft: "0",
                }}
              >
                {props.fileTitle}
              </th>

              {props.showDate && props.datePosition === "right" && (
                <th
                  className="table-viewer-heading"
                  style={{
                    textAlign: "right",
                    border: "0",
                    whiteSpace: "nowrap",
                    // marginRight: "0",
                    // paddingRight: "0",
                  }}
                >
                  {props.fileDate}
                </th>
              )}
            </tr>
          </thead>
        )}

        <tbody>
          {props.fileList.map((file, keyFile) => {
            // return <div key={keyFile}>{file.title}</div>;
            const created = formatDate(file.created, props.dateFormat);
            // console.log(props.exclude, " !== ", file.id);
            if (props.exclude !== file.id) {
              return (
                <tr
                  key={keyFile}
                  style={{ cursor: "pointer" }}
                  onClick={() => rowClick(file[props.fileIdField])}
                >
                  <td
                    className="table-viewer-row"
                    style={{
                      textAlign: "left",
                      paddingLeft: props.hover ? "12px" : 0,
                    }}
                  >
                    {/*
                     * Created (top position)
                     */}
                    {props.datePosition === "top" && (
                      <div className="table-viewer-created-top">{created}</div>
                    )}
                    {/*
                     * Title
                     */}
                    <a
                      className="table-viewer-title"
                      href={`${props.filesPath}/${file[props.fileIdField]}`}
                    >
                      {file.title}
                    </a>
                    {/*
                     * Appetizer
                     */}
                    {props.showAppetizer && (
                      <div className="table-viewer-appetizer">
                        {file.appetizer}
                      </div>
                    )}
                  </td>
                  {/*
                   * Created (right position)
                   */}
                  {props.showDate && props.datePosition === "right" && (
                    <td
                      className="table-viewer-row table-viewer-created-right"
                      style={{ textAlign: "right" }}
                    >
                      {created}
                    </td>
                  )}
                </tr>
              );
            }
          })}
        </tbody>
      </Table>
    );
  };

  const ListViewerEditMode = () => {
    return (
      <>
        <Card
          title={props.fileTitle}
          headerPadding="0 0 10px 0"
          bodyPadding="0 0 10px 0"
          border={0}
          maxWidthBody="100%"
          marginTop="12px"
          cardTitleClass="related-topics-heading"
        >
          <Sortable
            name="sortableTableViewer"
            items={props.fileList}
            setItems={props.setFileList}
            onValue={onSortableValue}
            onSortEnd={onSortEnd}
          />

          <br />

          <SubmitButton
            {...args}
            caption={t("Submit")}
            type="button"
            request={props.request}
            namespace={props.namespace}
            onSubmit={onSubmit}
          />
        </Card>
      </>
    );
  };

  const onSortEnd = (newOrder) => {
    // console.log(newOrder);
    // onSubmit(newOrder);
  };

  const toggleEdit = (id) => {
    refEdit[id].current.style.display =
      refEdit[id].current.style.display === "none" ? "block" : "none";
  };

  const onSortableValue = (value) => {
    // const record = JSON.parse(value);
    // const { id, language, status, submitter, title, ts, type } = value;
    const { id, title } = value;

    let fileid = null;
    if (typeof value[props.fileIdField] !== "undefined")
      fileid = value[props.fileIdField];

    const refID = id;
    refEdit[refID] = React.createRef();
    refTitles[refID] = React.createRef();

    // console.log(value);
    return (
      <>
        <span
          style={{
            whiteSpace: "nowrap",
            position: "relative",
            zIndex: 1000,
          }}
        >
          <a href={`${props.filesPath}/${fileid}`}>{title}</a>{" "}
          <span onClick={() => toggleEdit(refID)} style={{ cursor: "pointer" }}>
            <EditIcon style={{ color: "var(--dark)", fontSize: "1rem" }} />
          </span>
        </span>

        <div
          ref={refEdit[refID]}
          style={{ display: "none", margin: "10px 0 0 24px" }}
        >
          <Form.Group>
            <Form.Label>{t("Title")}</Form.Label>
            <Form.Control
              type="string"
              placeholder={""}
              name={`topic-${refID}`}
              ref={refTitles[refID]}
              defaultValue={title}
            />
          </Form.Group>
        </div>
      </>
    );
  };

  const getJSX = () => {
    if (props.fileList === null) return <></>;

    return (
      <>
        {getUserPermissionStatus(12) && contactMessageEditMode ? (
          <ListViewerEditMode />
        ) : (
          <ListViewerViewMode />
        )}
        <br />
        <Pagination
          {...args}
          size="sm"
          countItems={props.countRows}
          itemsPerPage={props.rowsPerPage}
          active={props.activePage}
          onClick={props.onPageClick}
        />
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
