/**
 * User CSS
 ============================================================================ */
import "./TextViewer.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "TextViewer", marginTop: 0 };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { textDescription } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
   * Refs
   ============================================================================ */
  const refViewer = React.createRef();

  /**
   * Effects
   ============================================================================ */
  React.useEffect(() => {
    refViewer.current.innerHTML = textDescription;

    // eslint-disable-next-line
  }, []);

  /**
   * Return
   ============================================================================ */
  return (
    <>
      <div ref={refViewer} style={{ marginTop: props.marginTop }}></div>
    </>
  );
};
