/**
 * CSS
 ============================================================================ */
import "./MediaInquiries.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col, Image } from "react-bootstrap";
// import Image from "react-bootstrap/Image";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";
import Appetizer from "../appetizer";
import TextViewer from "../textViewer";
import Editor from "../editor";
import ModalMessage from "../modalMessage";
import { useStickyState } from "../utils/state";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "MediaInquiries" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    contactMessageEditMode,
    textTitle,
    textDescription,
    textID,
    modalShow,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
    setModalShow,
  } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { url } = props.barrelProps;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  // let refShowBioPresident = React.useRef(false);
  const refModal = React.createRef();

  /**
    * Functions
    ============================================================================ */
  const textOnSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  const getJSX = () => {
    // Card component with common options
    return (
      <>
        <div className="media-inquiries" style={{}}>
          <Container fluid>
            <Row>
              <Col sm>
                <Appetizer
                  className="appetizer-media-inquiries"
                  title={t("Media Inquiries")}
                  transKey="MediaInquiries:Media Inquiries"
                  href={false}
                  {...args}
                >
                  Local-Currency Media Relations responds only to inquiries from
                  journalists, editors and other members of the media regarding
                  news stories. If you have a customer service inquiry, please
                  click "contact us" at the bottom of any Local-Currency.com
                  page
                </Appetizer>
              </Col>
              <Col sm>
                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={t("Regional Media Contacts")}
                  showContent={true}
                  href={`mailto:mediarelations-americas@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Corporate Media Relations (Americas)
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-asia@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Asia Region
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-australia@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Australia
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-canada@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Canada
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-europe-me-africa@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Europe, Middle East and Africa
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-uk@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency UK
                </Appetizer>

                <Appetizer
                  className="appetizer-regional-media-contacts"
                  title={false}
                  style={{ marginTop: "20px" }}
                  showContent={true}
                  href={`mailto:mediarelations-germany@local-currency.com`}
                  linkText={t("Send an email")}
                  transKey="MediaInquiries:Regional Media Contacts"
                  {...args}
                >
                  Local-Currency Germany
                </Appetizer>
              </Col>
            </Row>
          </Container>
        </div>
      </>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
