/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import Search from "../search";
import Editor from "../editor";
import TableViewer from "../tableViewer";
import Folders from "./Folders";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Explorer",
    title: "Explorer",
    titleFolders: "Folders",
    titleNewFile: "New File",
    editorSubject: "",
    searchable: true,
    searchHeadline: "How can we help?",
    searchSubtitle: false,
    showFiles: true,
    placeholderSearch: "",
    noFilesFoundMessage: "No files found.",
    navigation: false,
    navigationdefaultActiveKey: "",
    t: null,

    // Folders
    countFolders: null,
    foldersPerPage: null,
    activeFoldersPage: null,
    folderList: null,
    foldersAs: "ListGroup",
    folderSaveRequest: null,
    folderSaveRequestData: {},
    foldersPath: "/",
    foldersCountFilesField: "count_files",
    foldersEditAllowed: true,
    foldersShowFilesCount: true,
    folderName: "Folder",
    folderLink: "Link",
    setFolderList: () => {},

    // Files
    countFiles: null,
    filesPerPage: null,
    activeFilesPage: null,
    fileSaveRequest: null,
    fileSaveRequestData: {},
    fileUpdateRequest: null,
    filesTableStriped: true,
    filesTableDatePosition: "right",
    filesTableDateFormat: {
      month: "numeric",
      day: "numeric",
      year: "numeric",
    },
    filesTableHead: true,
    filesTableBorderless: false,
    filesTableHover: true,
    filesTableShowDate: true,
    filesTableShowAppetizer: false,
    fileList: null,
    filesPath: "/",
    fileIdField: "fileid",
    fileTitle: "Title",
    fileDate: "Date",
    setFileList: () => {},

    // Events
    onFoldersPageClick: () => {},
    onFolderClick: () => {},
    onFilesPageClick: () => {},
    onResetFilters: () => {},
    onFileSave: () => {},
    onSearchSubmit: () => {},
    onNavigationClick: () => {},
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { contactMessageEditMode } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getUserPermissionStatus } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Module vars
    ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  const getJSX = () => {
    return (
      <Card
        title={props.title}
        navigation={props.navigation}
        navigationdefaultActiveKey={props.navigationdefaultActiveKey}
        onNavigationClick={props.onNavigationClick}
        headerPadding="20px 20px 10px 20px"
        bodyPadding="20px 20px 10px 20px"
        border={0}
        headerBorderBottom={
          props.navigation === false ? 0 : "1px solid rgba(0,0,0,.125)"
        }
        maxWidthBody="100%"
      >
        <Container fluid>
          {/**
           * Search
           */}
          {props.searchable && (
            <>
              <Row>
                <Col>
                  {props.searchHeadline !== false && (
                    <div className="header-text">
                      <h1>{props.searchHeadline}</h1>
                    </div>
                  )}
                  {props.searchSubtitle !== false && (
                    <div className="header-text">
                      <h2>{props.searchSubtitle}</h2>
                    </div>
                  )}
                </Col>
              </Row>
              <Row>
                {props.foldersAs === "Select" && (
                  <Col>
                    <Folders
                      request={props.folderSaveRequest}
                      requestData={props.folderSaveRequestData}
                      path={props.foldersPath}
                      countFilesField={props.foldersCountFilesField}
                      editAllowed={props.foldersEditAllowed}
                      showFilesCount={props.foldersShowFilesCount}
                      folderList={props.folderList}
                      as={props.foldersAs}
                      setFolderList={props.setFolderList}
                      folderName={props.folderName}
                      folderLink={props.folderLink}
                      countFolders={props.countFolders}
                      foldersPerPage={props.foldersPerPage}
                      activePage={props.activeFoldersPage}
                      onPageClick={props.onFoldersPageClick}
                      onFolderClick={props.onFolderClick}
                      t={props.t}
                      {...args}
                    />
                  </Col>
                )}
                <Col>
                  <div style={{ textAlign: "center", margin: "0 0 35px 0" }}>
                    <div
                      style={{
                        display: "inline-block",
                        width: "100%",
                        maxWidth: "600px",
                      }}
                    >
                      <Search
                        {...args}
                        placeholder={props.placeholderSearch}
                        onSubmit={props.onSearchSubmit}
                      />
                    </div>
                  </div>
                </Col>
              </Row>
            </>
          )}

          <Row>
            {props.foldersAs === "ListGroup" && (
              <Col xl={4} style={{}}>
                {/**
                 * Folders Viewer
                 */}

                <Card
                  title={props.titleFolders}
                  header={props.titleFolders !== false}
                  headerPadding="0 0 10px 0"
                  bodyPadding="0 0 10px 0"
                  border={0}
                  maxWidthBody="100%"
                  marginTop="12px"
                  cardTitleClass="related-topics-heading"
                >
                  <Folders
                    request={props.folderSaveRequest}
                    requestData={props.folderSaveRequestData}
                    path={props.foldersPath}
                    countFilesField={props.foldersCountFilesField}
                    editAllowed={props.foldersEditAllowed}
                    showFilesCount={props.foldersShowFilesCount}
                    folderList={props.folderList}
                    as={props.foldersAs}
                    setFolderList={props.setFolderList}
                    folderName={props.folderName}
                    folderLink={props.folderLink}
                    countFolders={props.countFolders}
                    foldersPerPage={props.foldersPerPage}
                    activePage={props.activeFoldersPage}
                    onPageClick={props.onFoldersPageClick}
                    onFolderClick={props.onFolderClick}
                    t={props.t}
                    {...args}
                  />
                </Card>
              </Col>
            )}
            <Col xl={props.foldersAs === "ListGroup" ? 8 : 12} style={{}}>
              {/**
               * Files Viewer
               */}
              <TableViewer
                request={props.fileUpdateRequest}
                fileList={props.fileList}
                filesPath={props.filesPath}
                fileIdField={props.fileIdField}
                fileTitle={props.fileTitle}
                fileDate={props.fileDate}
                setFileList={props.setFileList}
                countRows={props.countFiles}
                rowsPerPage={props.filesPerPage}
                activePage={props.activeFilesPage}
                striped={props.filesTableStriped}
                datePosition={props.filesTableDatePosition}
                dateFormat={props.filesTableDateFormat}
                thead={props.filesTableHead}
                borderless={props.filesTableBorderless}
                hover={props.filesTableHover}
                showDate={props.filesTableShowDate}
                showAppetizer={props.filesTableShowAppetizer}
                onPageClick={props.onFilesPageClick}
                {...args}
              />

              {getUserPermissionStatus(13) && contactMessageEditMode && (
                <Card
                  title={props.titleNewFile}
                  headerPadding="0 0 10px 0"
                  bodyPadding="0 0 10px 0"
                  border={0}
                  maxWidthBody="100%"
                  marginTop="12px"
                  cardTitleClass="related-topics-heading"
                >
                  <br />
                  <Editor
                    {...args}
                    request={props.fileSaveRequest}
                    mode="html"
                    title=""
                    text=""
                    onSave={props.onFileSave}
                    data={props.fileSaveRequestData}
                  />
                  <br />
                </Card>
              )}
            </Col>
          </Row>
          <Row>
            <Col>{!props.showFiles && props.noFilesFoundMessage}</Col>
          </Row>
        </Container>
      </Card>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
