/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col, Navbar, Nav } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
import LanguageSelector from "../language-selector";

/**
 * Default export
 */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Footer" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { signedIn } = props.barrelGetters;
  const { setHelpPath1, setHelpPath2 } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const {
    defaultHelpTopic,
    defaultJobsTopic,
    defaultNewsTopic,
    pathHelpCategory,
    pathJobCategory,
    pathNewsCategory,
  } = props.barrelProps;

  /**
   * Destructured vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Module vars
   ============================================================================ */
  const now = new Date();

  /**
   * Functions
   ============================================================================ */

  /**
   * Implements the context sensitive help
   */
  const getHelpContext = () => {
    return `/${pathHelpCategory}/?search=`;
    const location = window.location.pathname;

    if (!signedIn) {
      if (location === "/" || location === "home") {
        return "/topic/common-questions"; // show the default help topic for Home
      }
    }

    if (
      location.indexOf("/topic/") === -1 &&
      location.indexOf("/article/") === -1
    ) {
      return `/topic${location}`;
    } else {
      return location;
    }
  };

  const getJSX = () => {
    return (
      <Card
        title=""
        header={true}
        border={0}
        maxWidthBody="100%"
        headerPadding="10px"
        bodyPadding="10px 20px"
      >
        {window.location.pathname !== "/auth" ? (
          /**
           * Full footer
           */
          <Container
            fluid
            style={{
              paddingRight: "0",
              paddingLeft: "0",
            }}
          >
            {/**
             * Top Footer
             */}
            <Row style={{ padding: 0 }}>
              <Col style={{ padding: 0 }}>
                <Navbar
                  collapseOnSelect
                  expand="sm"
                  style={{
                    borderRadius: "5px",
                    backgroundColor: "var(--light)", // e8f6f3
                    boxShadow: "none",
                    padding: "0 .5rem",
                  }}
                >
                  <Nav>
                    <Nav.Link
                      eventKey="help"
                      href={getHelpContext()}
                      onClick={() => {
                        setHelpPath1(pathHelpCategory);
                        setHelpPath2(defaultHelpTopic);
                      }}
                    >
                      {t("Help")}
                    </Nav.Link>
                  </Nav>

                  <Navbar.Toggle aria-controls="responsive-navbar-nav" />

                  <Navbar.Collapse id="responsive-navbar-nav">
                    {signedIn &&
                      window.location.pathname !== "/contact" &&
                      window.location.pathname !== "/account-registration" && (
                        <Nav>
                          <Nav.Link eventKey="contact" href="/contact">
                            {t("Contact")}
                          </Nav.Link>
                        </Nav>
                      )}

                    <Nav className="mr-auto">
                      <Nav.Link eventKey="about" href="/security">
                        {t("Security")}
                      </Nav.Link>
                    </Nav>

                    <Nav>
                      <Nav.Item>
                        <LanguageSelector {...args} />
                      </Nav.Item>
                    </Nav>
                  </Navbar.Collapse>
                </Navbar>
              </Col>
            </Row>

            {/*
             * Bottom Footer
             */}
            <Row
              className="d-none d-sm-block"
              style={{ padding: 0, fontSize: "0.86rem" }}
            >
              <Col style={{ padding: 0 }}>
                <Navbar
                  collapseOnSelect
                  expand="sm"
                  style={{
                    borderRadius: "5px",
                    backgroundColor: "var(--light)", // e8f6f3
                    boxShadow: "none",
                    padding: "0 .5rem",
                  }}
                >
                  <Nav>
                    <Nav.Link eventKey="about" href="/about">
                      {t("About")}
                    </Nav.Link>
                  </Nav>

                  <Nav>
                    <Nav.Link
                      eventKey="newsroom"
                      href="/newsroom"
                      onClick={() => {
                        setHelpPath1(pathNewsCategory);
                        setHelpPath2(defaultNewsTopic);
                      }}
                    >
                      {t("Newsroom")}
                    </Nav.Link>
                  </Nav>

                  <Nav>
                    <Nav.Link
                      eventKey="jobs"
                      href="/jobs"
                      onClick={() => {
                        setHelpPath1(pathJobCategory);
                        setHelpPath2(defaultJobsTopic);
                      }}
                    >
                      {t("Jobs")}
                    </Nav.Link>
                  </Nav>

                  <Nav className="mr-auto">
                    <Nav.Link eventKey="about" href="/sitemap">
                      {t("Sitemap")}
                    </Nav.Link>
                  </Nav>

                  <Nav>
                    <Nav.Item style={{}}>
                      &copy;&nbsp;{now.getFullYear()}
                    </Nav.Item>
                  </Nav>

                  <Nav>
                    <Nav.Link eventKey="about" href="/feedback">
                      {t("Feedback")}
                    </Nav.Link>
                  </Nav>
                </Navbar>
              </Col>
            </Row>
          </Container>
        ) : (
          /**
           * Minimal footer
           */
          <Container
            fluid
            style={{
              paddingRight: "0",
              paddingLeft: "0",
            }}
          >
            {/*
             * Top Footer
             */}
            <Row style={{ padding: 0 }}>
              <Col style={{ padding: 0 }}>
                <Nav className="justify-content-center">
                  <Nav.Item>
                    <LanguageSelector {...args} />
                  </Nav.Item>
                </Nav>
              </Col>
            </Row>
          </Container>
        )}
      </Card>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
