/**
 * CSS
 ============================================================================ */
import "./JobAppetizers.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import Appetizer from "../appetizer";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "JobAppetizers" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathJobCategory } = props.barrelProps;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  return (
    <div style={{ margin: "2rem 1rem" }}>
      <Container fluid>
        <Row>
          <Col sm>
            <Appetizer
              className="appetizer-jobs"
              title={t("Engineering")}
              href={`/${pathJobCategory}/?search=engineering`}
              linkText={t("View all jobs")}
              transKey="JobAppetizers:Engineering Appetizer"
              {...args}
            >
              As a software developer, architect, QA specialist or technical
              product manager, you’ll have the opportunity to invent solutions
              that transform the way people shop and do business.
            </Appetizer>
          </Col>
          <Col sm>
            <Appetizer
              className="appetizer-jobs"
              title={t("Product Management")}
              href={`/${pathJobCategory}/?search=Product%20%2BManagement`}
              linkText={t("View all jobs")}
              transKey="JobAppetizers:Product Management Appetizer"
              {...args}
            >
              As a product manager, you’ll lead the process of defining new
              ideas for customers and leading teams across every function,
              through every stage of development.
            </Appetizer>
          </Col>
          <Col sm>
            <Appetizer
              className="appetizer-jobs"
              title={t("Design & User Experience")}
              href={`/${pathJobCategory}/?search=Design%20%2BUser%20%2BExperience`}
              linkText={t("View all jobs")}
              transKey="JobAppetizers:Design & User Experience Appetizer"
              {...args}
            >
              Money is one of the most complex systems in the world. As a user
              experience practitioner, you will define experiences that make
              money easy and safe to access and share.
            </Appetizer>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
