/**
 * System imports
 ============================================================================ */
import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  // Redirect,
} from "react-router-dom";
import {
  // Navbar,
  // Nav,
  Container,
  Row,
  Col,
} from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import AccountRegistration from "../accountRegistration";
import Balance from "../accountContainer";
import { Transfer } from "../transfer";
import MyBusiness from "../myBusiness";
import UserEditor from "../userEditor";
import CurrencyContainer from "../currencyContainer";
import Editor from "../editor";
import Card from "../card";
import Status404 from "../status_404";
import Rating from "../rating";

/**
 * Component: Summary
 */
export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { namespace: "Summary" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    settings,
    userData,
    contactSubject,
    contactMessage,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getBusinesses } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  const pageBody = (
    <Router>
      <Switch>
        <Route
          exact
          path="/"
          component={() =>
            userData.uid !== null ? (
              <Balance {...args} />
            ) : (
              <AccountRegistration {...args} />
            )
          }
        />
        <Route
          exact
          path="/home"
          component={() =>
            userData.uid !== null ? (
              <Balance {...args} />
            ) : (
              <AccountRegistration {...args} />
            )
          }
        />
        <Route
          exact
          path="/summary"
          component={() =>
            userData.uid !== null ? (
              <Balance {...args} />
            ) : (
              <AccountRegistration {...args} />
            )
          }
        />
        <Route
          exact
          path="/account-registration"
          component={() => <AccountRegistration {...args} />}
        />
        <Route
          exact
          path="/profile"
          component={() => <UserEditor mode="profile" {...args} />}
        />
        {settings !== null &&
          typeof settings["currencies"] !== "undefined" &&
          (settings["currencies"] === null ||
            settings["currencies"].length === 0 ||
            settings["currencies"].length > 1) && (
            <Route
              exact
              path="/currency"
              component={() => <CurrencyContainer {...args} />}
            />
          )}

        <Route
          exact
          path="/business"
          component={() => <MyBusiness {...args} />}
        />
        <Route exact path="/account" component={() => <Balance {...args} />} />
        <Route
          exact
          path="/transfer"
          component={() => <Transfer {...args} />}
        />

        <Route
          exact
          path="/feedback"
          component={() => (
            <Editor title={contactSubject} text={contactMessage} {...args}>
              <Rating
                {...args}
                key="rating"
                style={{ marginBottom: "20px" }}
                inject="top"
              />
            </Editor>
          )}
        />

        {/**
         * Default route
         */}
        <Route path="*" component={() => <Status404 {...args} />} />
      </Switch>
    </Router>
  );

  return (
    <>
      {/* Header */}

      {/* Content */}
      {/*<h3>{t("Summary")}</h3>*/}

      <Container fluid>
        <Row>
          <Col>
            <Card
              backgroundHeader="Transparent"
              backgroundBody="Transparent"
              border="0"
              align="left"
              maxWidthBody="700px"
              header={false}
            >
              {pageBody}
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
};
