/**
 * CSS
 ============================================================================ */
import "./Sortable.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";
import {
  sortableContainer,
  sortableElement,
  sortableHandle,
} from "react-sortable-hoc";
import arrayMove from "array-move";
import { DragIndicator } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "Sortable",
    name: "sortable",
    items: [],
    setItems: () => {},
    dragHandle: (
      <span>
        <DragIndicator style={{ color: "var(--dark)" }} />
      </span>
    ),
    onValue: () => {},
    onSortEnd: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const DragHandle = sortableHandle(() => props.dragHandle);

  // const SortableItem = SortableElement(({ value }) => <li>{value}</li>);

  const SortableItem = sortableElement(({ value }) => {
    return (
      <li className="sortable">
        <DragHandle />
        {props.onValue(value)}
      </li>
    );
  });

  const SortableContainer = sortableContainer(({ children }) => {
    return <ul className="sortable">{children}</ul>;
  });

  // const SortableList = SortableContainer(({ items }) => {
  //   return (
  //     <ul>
  //       {items.map((value, index) => (
  //         <SortableItem key={`item-${value}`} index={index} value={value} />
  //       ))}
  //     </ul>
  //   );
  // });

  const onSortEnd = ({ oldIndex, newIndex }) => {
    const newOrder = arrayMove(props.items, oldIndex, newIndex);
    props.setItems(JSON.stringify(newOrder));
    props.onSortEnd(newOrder);
  };

  /**
   * Return
   ============================================================================ */
  return (
    <>
      {props.items !== null && (
        <SortableContainer onSortEnd={onSortEnd} useDragHandle>
          {props.items.map((value, index) => (
            <SortableItem
              key={`${props.name}-${index}`}
              index={index}
              value={value}
            />
          ))}
        </SortableContainer>
      )}
    </>
  );
};
