/**
 * CSS
 ============================================================================ */
import "./Newsroom.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import Appetizer from "../appetizer";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "JobAppetizers", onNavigationClick: () => {} };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { helpPath1 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathNewsCategory } = props.barrelProps;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const navigation = true
    ? [
        {
          name: t("Home"),
          link: "/newsroom",
        },
        {
          name: t("Latest News"),
          link: "/latest-news",
        },
        {
          name: t("About Us"),
          link: "/about",
        },
      ]
    : false;

  /**
    * Return
    ============================================================================ */
  return (
    <div className="newsroom" style={{ margin: "2rem 1rem" }}>
      <Card
        title={false}
        navigation={navigation}
        navigationdefaultActiveKey={helpPath1}
        onNavigationClick={(link) => setHelpPath(1, link)}
        headerPadding="0 20px 10px 20px"
        bodyPadding="20px 20px 10px 20px"
        border={0}
        headerBorderBottom={
          props.navigation === false ? 0 : "1px solid rgba(0,0,0,.125)"
        }
        maxWidthBody="100%"
      >
        <h1>{t("Latest News")}</h1>
        <Container fluid>
          <Row>
            <Col sm>
              <Appetizer
                className="appetizer-jobs"
                title={t(
                  "Back-to-School is About to Look Different. Here’s What Merchants Need to Know to Prepare."
                )}
                href={`/${pathNewsCategory}/?search=engineering`}
                linkText={t("View all news")}
                transKey="Newsroom:Appetizer 1"
                {...args}
              >
                In typical times, merchants play an important role in making the
                back-to-school experience simpler and less stressful for
                parents. Now, they have an even bigger opportunity to help
                shoppers navigate this tricky time of year. With such an unusual
                back-to-school season on the horizon, here’s what merchants need
                to keep in mind.
              </Appetizer>
            </Col>
            <Col sm>
              <Appetizer
                className="appetizer-jobs"
                title={t(
                  "As WNBA Season Tips Off, PayPal CEO Dan Schulman and Phoenix Mercury Star Diana Taurasi Exchange Notes On Working As One Team"
                )}
                href={`/${pathNewsCategory}/?search=Product%20%2BManagement`}
                linkText={t("View all news")}
                transKey="JobAppetizers:Product Management Appetizer"
                {...args}
              >
                In a recent #NeverStandStill conversation, Phoenix Mercury
                basketball player Diana Taurasi spoke to PayPal CEO Dan Schulman
                virtually from the WNBA’s “bubble,” where she and other
                players...
              </Appetizer>
            </Col>
            <Col sm>
              <Appetizer
                className="appetizer-jobs"
                title={t(
                  "How Melba Wilson is Keeping Her NYC Comfort Food Restaurant Afloat During COVID-19"
                )}
                href={`/${pathNewsCategory}/?search=Design%20%2BUser%20%2BExperience`}
                linkText={t("View all jobs")}
                transKey="JobAppetizers:Design & User Experience Appetizer"
                {...args}
              >
                When New York City restaurateur Melba Wilson first heard about
                the COVID-19 pandemic, she didn’t think it would come knocking
                on her eponymous eatery’s door. She is a resilient native New...
              </Appetizer>
            </Col>
          </Row>
        </Container>
      </Card>
    </div>
  );
};
