/**
 * System imports
 ============================================================================ */
import React, { cloneElement, useContext } from "react";
import { useTranslation } from "react-i18next";
import { Accordion, Card, AccordionContext } from "react-bootstrap";
import { useAccordionToggle } from "react-bootstrap/AccordionToggle";
import { ChevronRight, ExpandMore } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "AccordionCard",
    header: true,
    title: "Untitled",
    eventkey: "untitled",
    active: "",
    paddingBody: "0",
    // useRef: false,
    onClick: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const refCard = React.createRef();

  const CustomToggle = ({ children, eventKey }) => {
    const currentEventKey = useContext(AccordionContext);
    // const currentEventKey = React.useContext(AccordionContext);

    const isCurrentEventKey = currentEventKey === eventKey;

    // console.log("isCurrentEventKey:", isCurrentEventKey);

    if (refCard.current !== null) {
      refCard.current.open = !refCard.current.open;
      // console.log(
      //   "refCard.current.open:",
      //   // refCard.current.open,
      //   // props.eventkey,
      //   // eventKey,
      //   children
      // );
    }

    const decoratedOnClick = useAccordionToggle(eventKey, () => {
      // console.log("totally custom!", eventKey);
      props.onClick(eventKey);
    });
    // console.log(props.active, "===", eventKey);

    // const isCurrentEventKey = props.active === eventKey;
    // const isCurrentEventKey =
    //   refCard[props.eventkey].current !== null &&
    //   refCard[props.eventkey].current.open;
    // const isCurrentEventKey = false;

    // let isCurrentEventKey;

    // if (!props.useRef) {
    //   isCurrentEventKey = props.active === eventKey;
    // } else {
    //   isCurrentEventKey = refCard.current !== null && refCard.current.open;
    // }

    if (isCurrentEventKey) {
      return (
        <div onClick={decoratedOnClick}>
          {children}
          <span style={{ float: "right", marginLeft: "0.25rem" }}>
            <ExpandMore style={{ color: "var(--dark)", fontSize: "2rem" }} />
          </span>
        </div>
      );
    } else {
      return (
        <div onClick={decoratedOnClick}>
          {children}
          <span style={{ float: "right", marginLeft: "0.25rem" }}>
            <ChevronRight style={{ color: "var(--dark)", fontSize: "2rem" }} />
          </span>
        </div>
      );
    }
  };

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    let key = 0;
    return (
      <Card
        ref={refCard}
        style={{ border: 0, backgroundColor: "Transparent" }}
        open={false}
      >
        {props.header && (
          <Accordion.Toggle
            as={Card.Header}
            eventKey={props.eventkey}
            style={{
              backgroundColor: "Transparent",
              padding: "12px 2px 12px 2px",
              cursor: "pointer",
              fontSize: "20px",
            }}
          >
            <CustomToggle eventKey={props.eventkey}>{props.title}</CustomToggle>
          </Accordion.Toggle>
        )}
        <Accordion.Collapse eventKey={props.eventkey}>
          <Card.Body style={{ padding: props.paddingBody }}>
            {React.Children.map(args.children, (child) => {
              if (typeof child !== "object") {
                return child;
              } else {
                key++;
                return cloneElement(child, {
                  ...args,
                  styles: null,
                  key: key,
                });
              }
            })}
          </Card.Body>
        </Accordion.Collapse>
      </Card>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
