/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, Nav, Button } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Custom hooks
   */
  const { t } = useTranslation("Logout");

  /**
   * Barrels
   */
  const { error, info, language, emailChanged } = props.barrelGetters;
  const {
    setLanguage,
    // setRedirectTo,
    // setEmailChanged,
    // setLoggingOut,
    setInfo,
    setError,
  } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  const { onLogOut, onRedirect } = props.barrelEventHandlers;

  useEffect(() => {
    /**
     * Save vars
     */
    const tempError = error;
    const tempInfo = info;
    const tempLanguage = language;
    const tempEmailChanged = emailChanged;

    /**
     * Log out
     */
    onLogOut();

    /**
     * Restore vars
     */
    setLanguage(tempLanguage);

    if (tempEmailChanged) {
      onRedirect("/validate-email");
    }

    if (tempError !== null) {
      setError(JSON.stringify(tempError));
    } else {
      setError(null);
    }

    if (tempInfo !== null) {
      setInfo(JSON.stringify(tempInfo));
    } else {
      setInfo(null);
    }

    // eslint-disable-next-line
  }, []); // JSON.stringify(error), JSON.stringify(info) - logOut

  const onSubmit = (data) => {
    // console.log(data);
    // setRedirectTo("/home")
    // onRedirect("/home");
  };

  const CustomButton = React.forwardRef(
    ({ children, onChange, value }, ref) => {
      // console.log("value:", props.value);
      return (
        <>
          <Button>test</Button>
        </>
      );
    }
  );

  return (
    <>
      <Card
        backgroundHeader="Transparent"
        backgroundBody="Transparent"
        border="0"
        align="left"
        // maxWidth="500px"
        header={false}
      >
        <Form noValidate onSubmit={onSubmit}>
          <h4>{t("See you soon")}</h4>
          <hr />

          <Trans i18nKey="Logout:Security note">
            <strong>Security note:</strong> For security reasons, it is
            recommended that you close the browser completely at the end of your
            visit to the Local Currency website (i.e. all browser windows or
            browser tabs) and then open it again if necessary to visit other
            websites. This note applies in particular if you do not use your
            Local Currency website at home, but on a public device or a device
            that is accessible to other people - for example in an internet
            cafe, in a hotel or at work.
          </Trans>

          <br />
          <br />

          <Nav
            as={Button}
            block
            variant="primary"
            className="btn-outline-primary"
            style={{
              marginTop: 0,
              padding: 0,
            }}
          >
            <Nav.Item style={{ width: "100%" }}>
              <Nav.Link eventKey="home" href="/home">
                {t("Sign in")}
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </Form>
      </Card>
    </>
  );
};
