/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col, Form } from "react-bootstrap";

/**
   * User imports
   ============================================================================ */
import Card from "../card";
import Editor from "../editor";
import TextViewer from "../textViewer";
import Breadcrumb from "../breadcrumb";
import Search from "../search";

/**
   * Default export
   ============================================================================ */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "Article", searchable: true };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {
    contactMessageEditMode,
    textTitle,
    textAppetizer,
    textDescription,
    textID,
    helpPath2,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
  } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathJobCategory, pathHelpCategory } = props.barrelProps;

  /**
     * Module vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
     * Functions
     ============================================================================ */

  const jobOnSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  const urlEncode = (rawStr) => {
    // return rawStr.replace(/[\u00A0-\u9999<>\&]/gim, function (i) {
    //   return "&#" + i.charCodeAt(0) + ";";
    // });

    return rawStr.replace(/\+/g, "%2B");
  };

  const searchOnSubmit = (search) => {
    const searchUrlEncoded = urlEncode(search);
    window.location = `/${pathJobCategory}/?search=${searchUrlEncoded}`;
  };

  /**
     * Return
     ============================================================================ */
  return (
    <>
      <Card title={t("Help center")} border={0} maxWidthBody="100%">
        <Container fluid>
          {/**
           * Search
           */}
          {props.searchable && (
            <Row>
              <Col>
                <div className="header-text">
                  <h1>{t("How can we help?")}</h1>
                </div>
                <div style={{ textAlign: "center", margin: "0 0 35px 0" }}>
                  <div
                    style={{
                      display: "inline-block",
                      width: "100%",
                      maxWidth: "600px",
                    }}
                  >
                    <Search
                      {...args}
                      placeholder={t("Ask a question or search by keyword")}
                      onSubmit={searchOnSubmit}
                    />
                  </div>
                </div>
              </Col>
            </Row>
          )}
          <Row>
            <Col style={{}}>
              <Breadcrumb
                names={[t("Help"), t(helpPath2)]}
                links={[
                  `/${pathHelpCategory}`,
                  `/${pathHelpCategory}/${helpPath2}`,
                ]}
              />
              {/**
               * Viewer / Editor
               */}
              {!contactMessageEditMode ? (
                <TextViewer {...args} />
              ) : (
                <Editor
                  {...args}
                  request="save text"
                  mode="html"
                  title={textTitle}
                  appetizer={textAppetizer}
                  text={textDescription}
                  onSave={jobOnSave}
                  data={{
                    id: textID,
                    type: helpPath2,
                    status: 0,
                  }}
                />
              )}
            </Col>
          </Row>
        </Container>
      </Card>
    </>
  );
};
