/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, ListGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
import { Edit as EditIcon } from "@material-ui/icons";

/**
   * User imports
   ============================================================================ */
import SubmitButton from "../../submitButton";
import Sortable from "../../sortable";
import ListGroupItem from "../../listGroupItem";
import Pagination from "../../pagination";
// import Card from "../card";

/**
   * Default export
   ============================================================================ */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "Folders",
    request: null,
    requestData: {},
    path: "/",
    countFilesField: "count_files",
    folderList: null,
    folderName: "Folder",
    folderLink: "Link",
    countFolders: null,
    foldersPerPage: null,
    activePage: null,
    editAllowed: true,
    showFilesCount: true,
    as: "ListGroup", // ListGroup | Select
    onPageClick: () => {},
    onFolderClick: () => {},
    setFolderList: () => {},
    t: null,
  };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const { contactMessageEditMode, search, helpPath2 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Module vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Refs
   ============================================================================ */
  const refEdit = [];
  const refTopics = [];
  const refCat3 = [];
  const initialValues = {};

  /** 
     * Yup schema
     ============================================================================ */
  const schema = yup.object();

  /**
     * Functions
     ============================================================================ */

  /**
   * On Submit
   * @param {*} topics
   */
  const onSubmit = (topics) => {
    if (typeof topics === "undefined") topics = props.folderList;
    const topicsUpdated = updateTopics(topics);
    props.setFolderList(JSON.stringify(topicsUpdated));
    const data = { topics: topicsUpdated, ...props.requestData };
    return sendRequest({ req: props.request, data });
  };

  /**
   * Toggle Edit
   * @param {*} id
   */
  const toggleEdit = (id) => {
    refEdit[id].current.style.display =
      refEdit[id].current.style.display === "none" ? "block" : "none";
  };

  const updateTopics = (topics) => {
    const topicsUpdated = [];

    topics.map((topicString, index) => {
      const record = JSON.parse(topicString);
      const { id, topic, cat3 } = record;
      const refID = id !== null ? id : 0;

      if (refTopics[refID].current !== null) {
        record.topic = refTopics[refID].current.value;
      }
      if (refCat3[refID].current !== null) {
        record.cat3 = refCat3[refID].current.value;
      }

      topicsUpdated.push(JSON.stringify(record));
    });
    return topicsUpdated;
  };

  /**
   * On Sortable Value
   * @param {string} value, JSON string
   */
  const onSortableValue = (value) => {
    const record = JSON.parse(value);
    const { id, topic, cat3 } = record;

    let count_files = null;
    if (typeof record[props.countFilesField] !== "undefined")
      count_files = record[props.countFilesField];

    const refID = id !== null ? id : 0;

    refEdit[refID] = React.createRef();
    refTopics[refID] = React.createRef();
    refCat3[refID] = React.createRef();

    initialValues[`topic-${refID}`] = topic;
    initialValues[`cat3-${refID}`] = cat3;

    return (
      <>
        <span
          style={{
            whiteSpace: "nowrap",
            position: "relative",
            zIndex: 1000,
          }}
        >
          <a
            href={`${props.path}/${cat3}`}
            onClick={() => props.onFolderClick(cat3)}
          >
            {topic}
          </a>{" "}
          {count_files !== null && props.showFilesCount && `(${count_files}) `}
          <span onClick={() => toggleEdit(refID)} style={{ cursor: "pointer" }}>
            <EditIcon style={{ color: "var(--dark)", fontSize: "1rem" }} />
          </span>
        </span>

        <div
          ref={refEdit[refID]}
          style={{ display: "none", margin: "10px 0 0 24px" }}
        >
          <Form.Group>
            <Form.Label>{props.folderName}</Form.Label>
            <Form.Control
              type="string"
              placeholder={""}
              name={`topic-${refID}`}
              ref={refTopics[refID]}
              defaultValue={topic}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>{props.folderLink}</Form.Label>
            <Form.Control
              type="string"
              placeholder={""}
              name={`cat3-${refID}`}
              ref={refCat3[refID]}
              defaultValue={cat3}
            />
          </Form.Group>
        </div>
      </>
    );
  };

  const onSortEnd = (newOrder) => {
    // console.log(newOrder);
    // onSubmit(newOrder);
  };

  const FoldersList = () => {
    return (
      <>
        <ListGroup variant="flush" defaultActiveKey={1}>
          {props.folderList !== null &&
            props.folderList.map((item, index) => {
              // console.log("item:", item);
              const record = JSON.parse(item);
              const { id, topic, cat3 } = record;

              let count_files = null;
              if (typeof record[props.countFilesField] !== "undefined")
                count_files = record[props.countFilesField];

              if (id !== null) {
                const caption =
                  count_files !== null && props.showFilesCount
                    ? `${props.t(topic)} (${count_files})`
                    : props.t(topic);
                return (
                  <ListGroupItem
                    {...args}
                    caption={caption}
                    key={index}
                    href={`${props.path}/${cat3}?search=${search}`}
                    active={`${window.location.pathname}?search=${search}`}
                    cursor="pointer"
                    onClick={() => props.onFolderClick(cat3)}
                  />
                );
              }
            })}
        </ListGroup>
      </>
    );
  };

  const onChange = (e) => {
    // console.log("onChange: ", e.target.value);
    const cat3 = e.target.value;
    props.onFolderClick(cat3);
    window.location.href = `${props.path}/${cat3}?search=${search}`;
  };

  const FoldersSelect = () => {
    return (
      <>
        <Form.Control
          as="select"
          custom
          defaultValue={helpPath2}
          onChange={onChange}
        >
          <option value="" key={0}>
            {t("Select category")}
          </option>
          {props.folderList !== null &&
            props.folderList.map((item, index) => {
              // console.log("item:", item);
              const record = JSON.parse(item);
              const { id, topic, cat3 } = record;

              let count_files = null;
              if (typeof record[props.countFilesField] !== "undefined")
                count_files = record[props.countFilesField];

              if (id !== null) {
                const caption =
                  count_files !== null && props.showFilesCount
                    ? `${props.t(topic)} (${count_files})`
                    : props.t(topic);
                return (
                  <option value={cat3} key={index}>
                    {caption}
                  </option>
                );
              }
            })}
        </Form.Control>
      </>
    );
  };

  /**
     * Form
     ============================================================================ */
  const FoldersForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Sortable
        name="sortableFolders"
        items={props.folderList}
        setItems={props.setFolderList}
        onValue={onSortableValue}
        onSortEnd={onSortEnd}
      />

      <SubmitButton
        {...args}
        caption={props.t("Save Folders")}
        type="button"
        request={props.request}
        namespace={props.namespace}
        onSubmit={onSubmit}
      />
    </Form>
  );

  /**
     * Formik return
     ============================================================================ */
  return (
    <>
      {props.editAllowed && contactMessageEditMode ? (
        <Formik
          component={FoldersForm}
          validationSchema={schema}
          onSubmit={onSubmit}
          initialValues={initialValues}
        />
      ) : (
        <>{props.as === "ListGroup" ? <FoldersList /> : <FoldersSelect />}</>
      )}
      <br />
      <Pagination
        {...args}
        size="sm"
        countItems={props.countFolders}
        itemsPerPage={props.foldersPerPage}
        active={props.activePage}
        onClick={props.onPageClick}
      />
    </>
  );
};
