/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { ListGroup } from "react-bootstrap";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "ListGroupItem",
    caption: "Untitled",
    captionRight: null,
    key: "untitled",
    href: null, // "#",
    active: "",
    action: true,
    cursor: "default",
    onClick: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  // console.log("props:", props);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return (
      <ListGroup.Item
        action={props.action}
        key={props.key}
        eventKey={props.key}
        style={{ cursor: props.cursor, overflow: "hidden" }}
        href={props.href}
        active={props.href === props.active}
        onClick={props.onClick}
      >
        {props.captionRight !== null && (
          <span style={{ float: "right", margin: "0 0 5px 0" }}>
            {props.captionRight}
          </span>
        )}
        {props.caption}
      </ListGroup.Item>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
