/**
 * System imports
 ============================================================================ */
import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// import Image from "react-bootstrap/Image";

/**
  * User imports
  ============================================================================ */
import Summary from "../summary";
import Alert from "../alert";
import Login from "../login";
import Logout from "../logout";
import SignUp from "../signup";
import PasswordReset from "../passwordReset";
import ValidateEmail from "../validateEmail";
import ChangePassword from "../changePassword";
import Auth from "../auth";
import Footer from "../footer";
import MainMenu from "../mainMenu";
import Help from "../help";
// import Article from "../article";
import Jobs from "../jobs";
import Job from "../job";
import CoverPicture from "../coverPicture";
import JobAppetizers from "../jobAppetizers";
import Article from "../article";
import SiteMap from "../siteMap";
import About from "../about";
import MediaInquiries from "../mediaInquiries";
import Newsroom from "../newsroom";
import News from "../news";
import PressRelease from "../pressRelease";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { signedIn, search } = props.barrelGetters;
  const { setHelpPath1, setHelpPath2 } = props.barrelSetters;
  const { getRedirect, getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const {
    pathJobCategory,
    pathHelpCategory,
    pathNewsCategory,
    defaultJobsTopic,
  } = props.barrelProps;

  /**
   * Module constats
   ============================================================================ */
  const { wrapperClass, innerClass } = getContainerClass(signedIn);
  const appClass = signedIn ? "App" : "App bg";

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <>
        <Router>
          {getRedirect()}

          <div className={appClass} style={{}}>
            {/**
             * The alert
             */}
            <Alert data={args} />

            {/**
             * Wrapper for the main window
             */}
            <div className={wrapperClass} style={{}}>
              {/**
               * The main window
               */}
              <div
                className={innerClass}
                style={{
                  backgroundColor: "Transparent",
                }}
              >
                <MainMenu {...args} />

                <CoverPicture {...args} />

                {/**
                 * Routes
                 */}
                <Switch>
                  {/**
                   * Route: Sign up
                   */}
                  <Route
                    exact
                    path="/sign-up"
                    component={() => {
                      return SignUp(args);
                    }}
                  />

                  {/**
                   * Route: Sign up
                   */}
                  <Route
                    exact
                    path="/auth"
                    component={() => (signedIn ? Auth(args) : Login(args))}
                  />

                  {/**
                   * Route: Validate mail
                   */}
                  <Route
                    exact
                    path="/validate-email"
                    component={() => {
                      return ValidateEmail(args);
                    }}
                  />

                  {/**
                   * Route: Change password
                   */}
                  <Route
                    exact
                    path="/password"
                    component={() => {
                      return ChangePassword(args);
                    }}
                  />

                  {/**
                   * Route: Sign out
                   */}
                  <Route
                    exact
                    path="/sign-out"
                    component={() => {
                      return Logout(args);
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    path="/topic"
                    component={() => {
                      return Help(args);
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    exact
                    path="/jobs"
                    component={() => {
                      // return Jobs(args);
                      // return search === "" ? JobAppetizers(args) : Jobs(args);
                      // setHelpPath1("jobs-category");
                      // setHelpPath2(defaultJobsTopic);
                      return JobAppetizers(args);
                      // return <></>;
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    exact
                    path="/newsroom"
                    component={() => {
                      return Newsroom(args);
                    }}
                  />

                  {/**
                   * Route: News Category
                   */}
                  <Route
                    path={`/${pathNewsCategory}`}
                    component={() => {
                      return News(args);
                    }}
                  />

                  {/**
                   * Route: Job Category
                   */}
                  <Route
                    path={`/${pathJobCategory}`}
                    component={() => {
                      return Jobs(args);
                    }}
                  />

                  {/**
                   * Route: Job
                   */}
                  <Route
                    path="/job"
                    component={() => {
                      return Job(args);
                    }}
                  />

                  {/**
                   * Route: Release
                   */}
                  <Route
                    path="/release"
                    component={() => {
                      return PressRelease(args);
                    }}
                  />

                  {/**
                   * Route: Help Category
                   */}
                  <Route
                    path={`/${pathHelpCategory}`}
                    component={() => {
                      return Help(args);
                    }}
                  />

                  {/**
                   * Route: Help
                   */}
                  <Route
                    path="/help"
                    component={() => {
                      return Article(args);
                    }}
                  />

                  {/**
                   * Route: Site Map
                   */}
                  <Route
                    exact
                    path="/sitemap"
                    component={() => <SiteMap {...args} />}
                  />

                  {/**
                   * Route: About
                   */}
                  <Route
                    exact
                    path="/about"
                    component={() => <About {...args} />}
                  />

                  {/**
                   * Route: Media Inquiries
                   */}
                  <Route
                    exact
                    path="/media-inquiries"
                    component={() => <MediaInquiries {...args} />}
                  />

                  {/**
                   * Route: Password reset
                   */}
                  <Route
                    exact
                    path="/reset"
                    component={() => {
                      return PasswordReset(args);
                    }}
                  />

                  {/**
                   * Default route
                   */}
                  <Route
                    path="*"
                    component={() => {
                      return signedIn ? Summary(args) : Login(args);
                    }}
                  />
                </Switch>
                <Footer {...args} />
              </div>
            </div>
          </div>
        </Router>
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
