/**
 * System imports
 ============================================================================ */
import "./EditModeSwitch.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";
import { Form } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "EditModeSwitch",
    label: "Edit",
    onChange: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { contactMessageEditMode } = props.barrelGetters;
  const { setContactMessageEditMode } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const toggleMode = (event) => {
    setContactMessageEditMode(!contactMessageEditMode);
    props.onChange(!contactMessageEditMode);
  };

  /**
   * Return
   ============================================================================ */
  return (
    <>
      <Form.Check
        type="switch"
        id="switchEditMode"
        custom
        className="variant-light"
        label={props.label}
        checked={contactMessageEditMode}
        onChange={toggleMode}
      />
    </>
  );
};
