/**
 * CSS
 ============================================================================ */
import "./Appetizer.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import InjectChildren from "../injectChildren";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "Appetizer",
    style: {},
    className: "appetizer-1",
    title: "Untitled",
    href: "#",
    linkText: "more...",
    showTitle: true,
    showContent: true,
    showLink: true,
    onLinkClick: () => {},
    onLinkMouseDown: () => {},
    transKey: "JobAppetizers:Engineering Appetizer",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  // let title_class = props.compact ? "compact " : "";
  // title_class += props.muteTitle ? "text-muted " : "";
  // const p_class = props.compact ? "compact" : "";
  return (
    <div className={`appetizer ${props.className}`} style={props.style}>
      {props.showTitle && props.title !== false && (
        <p className={`appetizer-title ${props.className}`}>{props.title}</p>
      )}

      {props.showContent && (
        <p className={`appetizer-content ${props.className}`}>
          <Trans i18nKey={props.transKey}>
            <InjectChildren {...args} />
          </Trans>
        </p>
      )}

      {props.showLink && props.href !== false && (
        <p className={`appetizer-link ${props.className}`}>
          <a
            href={props.href}
            onClick={props.onLinkClick}
            onMouseDown={props.onLinkMouseDown}
          >
            {props.linkText}
          </a>
        </p>
      )}
    </div>
  );
};
