/**
 * CSS
 ============================================================================ */
import "./Rating.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Rating", style: {} };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return (
      <div style={props.style}>
        <Container
          fluid
          style={{
            fontSize: "0.9rem",
            textAlign: "center",
            whiteSpace: "nowrap",
          }}
        >
          <Row>
            <Col
              style={{ padding: "0", marginBottom: "10px", textAlign: "left" }}
            >
              <Trans i18nKey="Rating:Thank you for your feedback">
                Thank you for your feedback! How helpful was our site?*
              </Trans>
            </Col>
          </Row>
          <Row>
            <Col style={{ padding: "0" }}>
              <Form.Check
                custom
                inline
                type="radio"
                label=""
                name="rating"
                id="rating1"
              />
            </Col>
            <Col style={{ padding: "0" }}>
              <Form.Check
                custom
                inline
                type="radio"
                label=""
                name="rating"
                id="rating2"
              />
            </Col>
            <Col style={{ padding: "0" }}>
              <Form.Check
                custom
                inline
                type="radio"
                label=""
                name="rating"
                id="rating3"
              />
            </Col>
            <Col style={{ padding: "0" }}>
              <Form.Check
                custom
                inline
                type="radio"
                label=""
                name="rating"
                id="rating4"
              />
            </Col>
            <Col style={{ padding: "0" }}>
              <Form.Check
                custom
                inline
                type="radio"
                label=""
                name="rating"
                id="rating5"
              />
            </Col>
          </Row>
          <Row style={{ marginTop: "-0.3rem" }}>
            <Col style={{ padding: "0" }}>{t("1")}</Col>
            <Col style={{ padding: "0" }}>{t("2")}</Col>
            <Col style={{ padding: "0" }}>{t("3")}</Col>
            <Col style={{ padding: "0" }}>{t("4")}</Col>
            <Col style={{ padding: "0" }}>{t("5")}</Col>
          </Row>
          <Row>
            <Col style={{ padding: "0" }}>{t("Not helpful")}</Col>
            <Col style={{ padding: "0" }}></Col>
            <Col style={{ padding: "0" }}></Col>
            <Col style={{ padding: "0" }}></Col>
            <Col style={{ padding: "0" }}>{t("Very helpful")}</Col>
          </Row>
        </Container>
      </div>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
