/**
 * CSS
 ============================================================================ */
import "./About.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col, Image } from "react-bootstrap";
// import Image from "react-bootstrap/Image";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import Appetizer from "../appetizer";
import TextViewer from "../textViewer";
import Editor from "../editor";
import ModalMessage from "../modalMessage";
import { useStickyState } from "../utils/state";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "About" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    contactMessageEditMode,
    textTitle,
    textAppetizer,
    textDescription,
    textID,
    modalShow,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
    setModalShow,
  } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { url } = props.barrelProps;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  // let refShowBioPresident = React.useRef(false);
  const refModal = React.createRef();

  /**
   * Functions
   ============================================================================ */
  const textOnSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  const getJSX = () => {
    // Card component with common options
    return (
      <>
        <div className="about" style={{}}>
          <h1>{t("Who We Are")}</h1>
          {/**
           * Viewer / Editor
           */}
          {!contactMessageEditMode ? (
            <TextViewer {...args} />
          ) : (
            <Editor
              {...args}
              request="save text"
              mode="html"
              title={textTitle}
              appetizer={textAppetizer}
              text={textDescription}
              onSave={textOnSave}
              data={{
                id: textID,
                type: "about",
                status: 0,
              }}
            />
          )}
        </div>

        <div className="about" style={{ backgroundColor: "white" }}>
          <Container fluid>
            <Row>
              <Col sm>
                <Appetizer
                  className="appetizer-about"
                  title={t("Founding Date")}
                  transKey="JobAppetizers:Engineering Appetizer"
                  href={false}
                  {...args}
                >
                  May 2020
                </Appetizer>
              </Col>
              <Col sm>
                <Appetizer
                  className="appetizer-about"
                  title={t("Website Address")}
                  showContent={false}
                  href={`https://local-currency.com/`}
                  linkText={t("local-currency.com")}
                  transKey="About:Engineering Appetizer"
                  {...args}
                ></Appetizer>
              </Col>
              <Col sm>
                <Appetizer
                  className="appetizer-about"
                  title={t("Media Inquiries")}
                  href={`/media-inquiries`}
                  linkText={t("Contact Us")}
                  transKey="JobAppetizers:Product Management Appetizer"
                  {...args}
                >
                  Instructions for media inquires to any of our regions.
                </Appetizer>
              </Col>
            </Row>
          </Container>
        </div>

        <div className="about" style={{}}>
          <h1>{t("Leadership")}</h1>

          <Image
            className="d-none d-md-block"
            style={{ marginTop: "60px" }}
            src={`${url}/gfx/armands_strazds.png`}
            fluid
          />
          <Appetizer
            className="appetizer-leadership text-muted"
            title={t("Armands Strazds")}
            muteTitle={true}
            compact={true}
            style={{ margin: "20px 0 0 0" }}
            href={`#`}
            onLinkMouseDown={() => setModalShow(true)}
            linkText={t("Read Bio")}
            transKey="About:President"
            {...args}
          >
            President and CEO, Local-Currency
          </Appetizer>

          <Appetizer
            className="appetizer-leadership text-muted"
            title={t("Local Currencies Governing Council")}
            muteTitle={true}
            compact={true}
            style={{ margin: "20px 0 0 0" }}
            href={false}
            {...args}
          >
            International Experts in Economics and Finance, Research Analysts
          </Appetizer>

          <ModalMessage
            show={modalShow}
            path3="articles"
            file="BioArmandsStrazds.js"
            size="lg"
            centered={false}
            title={t("Armands Strazds")}
            footer=""
            onHide={() => setModalShow(false)}
            {...args}
          />
        </div>
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
