/**
 * User CSS
 ============================================================================ */
import "./RichTextToolbar.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import {
  Navbar,
  Nav,
  NavDropdown,
  Form,
  FormControl,
  InputGroup,
} from "react-bootstrap";
import { useTranslation } from "react-i18next";
import {
  Title as FormatTitle,
  TextFormat,
  FormatBold,
  FormatItalic,
  FormatUnderlined,
  FormatStrikethrough,
  FormatListBulleted,
  FormatListNumbered,
  Undo,
  Redo,
  FormatClear,
  FormatAlignLeft,
  FormatAlignRight,
  FormatAlignCenter,
  FormatAlignJustify,
  FormatIndentIncrease,
  FormatIndentDecrease,
  InsertLink,
} from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */

/**
 * Deafault export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "RichTextToolbar",
    request: "get something",
    onFormat: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { richTextToolbarLinkVisible } = props.barrelGetters;
  const { setRichTextToolbarLinkVisible } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const refCreateLink = React.createRef();

  /**
   * Functions
   ============================================================================ */

  const formatDoc = (sCmd, sValue) => {
    // console.log("formatDoc:", sCmd, sValue);
    document.execCommand(sCmd, false, sValue);
    props.onFormat();
  };

  return (
    <Navbar
      expand="lg"
      style={{
        boxShadow: "none",
        backgroundColor: "Transparent",
        border: "1px solid var(--border)",
        borderBottom: "none",
        borderTopLeftRadius: ".25rem",
        borderTopRightRadius: ".25rem",
        padding: 0,
        // overflowX: "auto",
        // msOverflowY: "visible",
      }}
    >
      {/* Title format */}
      <NavDropdown
        title={<FormatTitle style={{ color: "var(--dark)" }} />}
        id="richtext-toolbar-title-format"
        onSelect={(selectedKey) => formatDoc("formatblock", selectedKey)}
        className="richtext-formatting"
      >
        <NavDropdown.Item eventKey="h1">
          <h1>Heading 1</h1>
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="h2">
          <h2>Heading 2</h2>
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="h3">
          <h3>Heading 3</h3>
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="h4">
          <h4>Heading 4</h4>
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="h5">
          <h5>Heading 5</h5>
        </NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item eventKey="p">Paragraph</NavDropdown.Item>
        <NavDropdown.Item eventKey="pre">Preformatted</NavDropdown.Item>
      </NavDropdown>

      {/* Text format */}
      <NavDropdown
        title={<TextFormat style={{ color: "var(--dark)" }} />}
        id="richtext-toolbar-text-format"
        onSelect={(selectedKey) => formatDoc(selectedKey)}
        className="richtext-formatting"
      >
        <NavDropdown.Item eventKey="bold">
          <FormatBold style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="italic">
          <FormatItalic style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="underline">
          <FormatUnderlined style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="strikethrough">
          <FormatStrikethrough style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item eventKey="subscript">
          <span style={{ color: "var(--dark)" }}>
            <strong>
              A<sub>1</sub>
            </strong>
          </span>
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="superscript">
          <span style={{ color: "var(--dark)" }}>
            <strong>
              A<sup>1</sup>
            </strong>
          </span>
        </NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item eventKey="removeFormat">
          <FormatClear style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
      </NavDropdown>

      {/* Text justify */}
      <NavDropdown
        title={<FormatAlignLeft style={{ color: "var(--dark)" }} />}
        id="richtext-toolbar-text-justify"
        onSelect={(selectedKey) => formatDoc(selectedKey)}
        className="richtext-formatting"
      >
        <NavDropdown.Item eventKey="justifyCenter">
          <FormatAlignCenter style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="justifyFull">
          <FormatAlignJustify style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="justifyLeft">
          <FormatAlignLeft style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="justifyRight">
          <FormatAlignRight style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>

        <NavDropdown.Divider />

        <NavDropdown.Item eventKey="indent">
          <FormatIndentIncrease style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
        <NavDropdown.Item eventKey="outdent">
          <FormatIndentDecrease style={{ color: "var(--dark)" }} />
        </NavDropdown.Item>
      </NavDropdown>

      {/* ... the rest */}
      <Navbar.Toggle aria-controls="basic-navbar-nav" className="ml-auto" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          {!richTextToolbarLinkVisible && (
            <>
              <Nav.Link onClick={() => formatDoc("insertunorderedlist")}>
                <FormatListBulleted style={{ color: "var(--dark)" }} />
              </Nav.Link>
              <Nav.Link onClick={() => formatDoc("insertorderedlist")}>
                <FormatListNumbered style={{ color: "var(--dark)" }} />
              </Nav.Link>
              <Nav.Link onClick={() => formatDoc("undo")}>
                <Undo style={{ color: "var(--dark)" }} />
              </Nav.Link>
              <Nav.Link onClick={() => formatDoc("redo")}>
                <Redo style={{ color: "var(--dark)" }} />
              </Nav.Link>
            </>
          )}

          {richTextToolbarLinkVisible && (
            <Nav.Link>
              <FormControl
                type="text"
                ref={refCreateLink}
                size="sm"
                placeholder={t("Link")}
                className="rich-text-toolbar-textbox mr-sm-2"
              />
            </Nav.Link>
          )}

          <Nav.Link
            onClick={() => {
              if (richTextToolbarLinkVisible) {
                formatDoc("createlink", refCreateLink.current.value);
                setRichTextToolbarLinkVisible(false);
              } else {
                setRichTextToolbarLinkVisible(true);
              }
            }}
          >
            <InsertLink style={{ color: "var(--dark)" }} />
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};
