/**
 * CSS
 ============================================================================ */
import "./News.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
// import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Explorer from "../explorer/Explorer";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "News", searchable: true };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    helpPath1,
    helpPath2,
    countSortableListItems,
    topicsPerPage,
    activePageTopics,
    countTexts,
    textItemsPerPage,
    activePageTexts,
    textTitlesList,
    topicsList,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
    setTextTitlesList,
    setTopicsList,
  } = props.barrelSetters;
  const {
    setHelpPath,
    getUserPermissionStatus,
    getTopics,
    getTextsList,
    getFileLinkFromURI,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathNewsCategory } = props.barrelProps;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  /**
   * On view all click
   */
  const onViewAllNewsClick = () => {
    setHelpPath(2, "");
  };

  /**
   * On save
   * @param {*} param0
   */
  const onNewsSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  /**
   * URL encode string
   * @param {*} rawStr
   */
  const urlEncode = (rawStr) => {
    return rawStr.replace(/\+/g, "%2B");
  };

  /**
   * On Search submit
   * @param {*} search
   */
  const onSearchSubmit = (search) => {
    const searchUrlEncoded = urlEncode(search);
    window.location = `/${pathNewsCategory}/?search=${searchUrlEncoded}`;
  };

  const noFilesFoundMessage = (
    <Trans i18nKey="News:Not found">
      No news found using your search criteria. Please try different criteria or
      click{" "}
      <a href={`/${pathNewsCategory}/?search=`} onClick={onViewAllNewsClick}>
        here
      </a>{" "}
      to view all news.
    </Trans>
  );

  const onHelpPath1Click = (link) => {
    setHelpPath(1, link);
  };

  const navigation = true
    ? [
        {
          name: t("Home"),
          link: "/newsroom",
        },
        {
          name: t("Latest News"),
          link: "/latest-news",
        },
        {
          name: t("About Us"),
          link: "/about",
        },
      ]
    : false;

  /**
    * Return
    ============================================================================ */
  return (
    <>
      <Explorer
        t={t}
        searchable={props.searchable}
        searchHeadline={t("Latest News")}
        searchSubtitle={t("The latest headlines from Local-Currency.")}
        title={false}
        navigation={navigation}
        navigationdefaultActiveKey={helpPath1}
        onNavigationClick={onHelpPath1Click}
        titleFolders={t("Category")}
        titleNewFile={t("News")}
        foldersAs="Select"
        folderList={topicsList}
        fileList={textTitlesList}
        fileTitle={"News"}
        fileDate={"Date"}
        setFolderList={setTopicsList}
        setFileList={setTextTitlesList}
        showFiles={textTitlesList !== null}
        filesTableStriped={false}
        filesTableDatePosition="top"
        filesTableDateFormat={{
          month: "short",
          day: "numeric",
          year: "numeric",
        }}
        filesTableHead={false}
        filesTableBorderless={true}
        filesTableHover={false}
        filesTableShowAppetizer={true}
        fileSaveRequest="save text"
        fileUpdateRequest="update texts"
        folderSaveRequest="save topics"
        fileSaveRequestData={{
          id: null,
          type: helpPath2,
          status: 0,
        }}
        folderSaveRequestData={{
          cat1: "news",
          cat2: pathNewsCategory,
        }}
        foldersPath={`/${pathNewsCategory}`}
        filesPath="/release"
        foldersCountFilesField="count_texts"
        fileIdField="textid"
        foldersEditAllowed={getUserPermissionStatus(11)}
        folderName={t("Topic")}
        folderLink={t("Link")}
        noFilesFoundMessage={noFilesFoundMessage}
        placeholderSearch={t("News Title or Category")}
        countFolders={countSortableListItems}
        foldersPerPage={topicsPerPage}
        activeFoldersPage={activePageTopics}
        onFoldersPageClick={(number) => getTopics({ newPage: number })}
        onFolderClick={(link) => setHelpPath(2, link)}
        countFiles={countTexts}
        filesPerPage={textItemsPerPage}
        activeFilesPage={activePageTexts}
        onFilesPageClick={(number) => getTextsList({ newPage: number })}
        onFileSave={onNewsSave}
        onResetFilters={onViewAllNewsClick}
        onSearchSubmit={onSearchSubmit}
        {...args}
      />
    </>
  );
};
