/**
 * System CSS
 ============================================================================ */
// import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

/**
 * User CSS
 ============================================================================ */
// import "./react-draft-wysiwyg.css";
import "./Editor.css";

/**
 * System imports
 ============================================================================ */
import React, { useState, useEffect } from "react";
import { Form, InputGroup, Accordion } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";
// import Rating from "../rating";
import AccordionCard from "../accordionCard";

import RichText from "../richText";
import InjectChildren from "../injectChildren";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Editor",
    request: "send message",
    mode: "text", // text | html
    data: {},
    title: "",
    appetizer: "",
    text: "",
    onSave: () => {},
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    language,
    // accountNumber,
    userData,
    accordionViewEditor,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest, showMessage } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  const refSubject = React.createRef();
  const refAppetizer = React.createRef();
  const refMessage = React.createRef();
  // let refAccordionViewEditor = React.useRef(null);
  // const refMessageRaw = React.useRef(null);

  // console.log("editorLabels:", EditorLabels());

  /**
    * Functions
    ============================================================================ */
  const onSubmit = () => {
    const data = props.data;
    // const link = getFileLinkFromURI();

    if (data.type === "") {
      showMessage("CATEGORY_NOT_SELECTED", "info", false);
      return;
    }

    data.sender = userData.uid;
    data.language = language;
    data.message = refMessage.current.value; // innerHTML
    data.appetizer = refAppetizer.current.value;
    data.subject = refSubject.current.value;
    // data.link = link; // createKey(refSubject.current.value);

    console.log(data);
    // return;

    props.onSave({
      title: refSubject.current.value,
      text: refMessage.current.value,
      appetizer: refAppetizer.current.value,
    });

    sendRequest({ req: props.request, data: data });
  };

  const richTextOnChange = (data) => {
    refMessage.current.value = data;
  };

  const richTextOnBlur = (data) => {
    // console.log(data);
    // setHtmlCode(data);
    // refMessage.current.innerHTML = data;
    // setContactMessage(refMessage.current.innerHTML);
  };

  // // refAccordionViewEditor = React.useRef(null);
  // const ToggleEditor = (event) => {
  //   // refMessage.current.visible = !refMessage.current.visible;
  //   // console.log("refMessage.current.visible:", refMessage.current.visible);
  // };

  /**
    * Form
    ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card header={false} border={0} bodyPadding={0} maxWidthBody="700px">
      <Form noValidate onSubmit={handleSubmit}>
        {/*
         * Children Injection Point: Top
         */}
        <InjectChildren {...args} inject="top" />

        {/*
         * Subject
         */}
        <Form.Group controlId="validationFormikSubject">
          <Form.Label>{t("Subject")}</Form.Label>

          <Form.Control
            type="string"
            placeholder={t("Enter message subject")}
            aria-describedby="inputGroupPrepend"
            name="subject"
            ref={refSubject}
            defaultValue={props.title}
          />
          <Form.Control.Feedback type="invalid"></Form.Control.Feedback>
        </Form.Group>

        {/*
         * Appetizer
         */}
        <Form.Group controlId="messageText">
          <Accordion defaultActiveKey={accordionViewEditor}>
            <AccordionCard title={t("Appetizer")} eventkey="html-code">
              <>
                <Form.Control
                  placeholder={t("Enter appetizer")}
                  name="appetizer"
                  ref={refAppetizer}
                  defaultValue={props.appetizer}
                  as="textarea"
                  style={{ overflowX: "auto", whiteSpace: "nowrap" }}
                  rows="5"
                />
              </>
            </AccordionCard>
          </Accordion>
        </Form.Group>

        {/*
         * HTML Message
         */}
        {props.mode === "html" && (
          <Form.Group controlId="messageHTML">
            <Form.Label>{t("Message")}</Form.Label>

            <RichText
              value={
                refMessage.current !== null
                  ? refMessage.current.value
                  : props.text
              }
              onChange={richTextOnChange}
              onBlur={richTextOnBlur}
              {...args}
            />
          </Form.Group>
        )}

        {/*
         * Text Messag | HTML Code
         */}
        <Form.Group controlId="messageText">
          {props.mode === "html" ? (
            <Accordion defaultActiveKey={accordionViewEditor}>
              <AccordionCard title={t("HTML code")} eventkey="html-code">
                <>
                  <Form.Control
                    placeholder={t("Enter message")}
                    name="message"
                    ref={refMessage}
                    value={values.message}
                    onChange={handleChange}
                    as="textarea"
                    style={{ overflowX: "auto", whiteSpace: "nowrap" }}
                    rows="10"
                  />
                </>
              </AccordionCard>
            </Accordion>
          ) : (
            // props.mode === "text"
            <>
              <Form.Label>{t("Message")}</Form.Label>
              <Form.Control
                placeholder={t("Enter message")}
                name="message"
                ref={refMessage}
                value={values.message}
                onChange={handleChange}
                as="textarea"
                rows="10"
              />
            </>
          )}
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          type="button"
          request={props.request}
          namespace={props.namespace}
          onSubmit={onSubmit}
        />

        {/*
         * Children Injection Point: Bottom
         */}
        <InjectChildren {...args} inject="bottom" />
      </Form>
    </Card>
  );

  /**
    * Formik return
    ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      onSubmit={onSubmit}
      initialValues={{
        subject: props.title,
        appetizer: props.appetizer,
        message: props.text,
      }}
    />
  );
};
