/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import Image from "react-bootstrap/Image";

/**
 * User imports
 ============================================================================ */
import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Component" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { signedIn } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathJobCategory } = props.barrelProps;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  const getJSX = () => {
    let imgSrc = "hands_helping.png"; //  null;

    // console.log("window.location.pathname:", window.location.pathname);

    switch (window.location.pathname) {
      case "/":
        if (!signedIn) imgSrc = "raised_hands.png";
        break;

      case "/sign-in":

      case "/home":
        imgSrc = "raised_hands.png";
        break;

      case "/sign-out":
        imgSrc = "evening_band.png";
        // imgSrc = "evening_cave.png";
        break;

      case "/account":
        imgSrc = "rain_hand.png";
        break;

      case "/currency":
        imgSrc = "money_plants.png";
        break;

      case "/business":
        imgSrc = "hands_joined.png";
        break;

      case "/transfer":
        imgSrc = "flower_hands.png";
        break;

      case "/jobs":
        imgSrc = "vacation_job.png";
        break;

      default:
        if (
          window.location.pathname.substr(0, `/${pathJobCategory}/`.length) ===
            `/${pathJobCategory}/` ||
          window.location.pathname.substr(0, "/job/".length) === "/job/" ||
          window.location.pathname.substr(0, "/jobs/".length) === "/jobs/"
        ) {
          imgSrc = "vacation_job.png";
        }
        break;
    }

    if (imgSrc !== null) {
      return (
        <Image
          className="d-none d-md-block"
          src={`${window.location.protocol}//${window.location.hostname}:${window.location.port}/gfx/${imgSrc}`}
          fluid
        />
      );
    } else {
      return <></>;
    }
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
