/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import LinkList from "../linkList";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "SiteMap" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  const { setHelpPath1, setHelpPath2 } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathHelpCategory, defaultHelpTopic } = props.barrelProps;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  /**
    * Return
    ============================================================================ */
  return (
    <div style={{ margin: "2rem 1rem" }}>
      <Container fluid>
        <Row>
          <Col sm>
            <LinkList title={t("Local-Currency Account")}>
              <ul>
                <li>
                  <a href="/sign-in">{t("Local-Currency Log In")}</a>
                </li>
                <li>
                  <a href="/sign-up">{t("Local-Currency Sign Up")}</a>
                </li>
              </ul>
            </LinkList>
          </Col>
          <Col sm>
            <LinkList title={t("Help and Security")}>
              <ul>
                <li>
                  <a
                    href={`/${pathHelpCategory}/?search=`}
                    onClick={() => {
                      setHelpPath1(pathHelpCategory);
                      setHelpPath2(defaultHelpTopic);
                    }}
                  >
                    {t("Local-Currency Help Center")}
                  </a>
                </li>
              </ul>
            </LinkList>
          </Col>
          <Col sm>
            <LinkList title={t("Social Media")}>
              <ul>
                <li>
                  <a href="https://www.facebook.com/LocalCurrencyCOM">
                    Local-Currency on Facebook
                  </a>
                </li>
                <li>
                  <a href="https://twitter.com/currency_local">
                    Local-Currency on Twitter
                  </a>
                </li>
                <li>
                  <a href="https://www.linkedin.com/groups/8952390/">
                    Local-Currency on LinkedIn
                  </a>
                </li>

                <li>
                  <a href="https://www.youtube.com/channel/UCS4v_kTX5zjAEM80h9LAPEQ">
                    Local-Currency on YouTube
                  </a>
                </li>
              </ul>
            </LinkList>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
