/**
 * System imports
 ============================================================================ */
import React, { useRef } from "react";
import {
  Accordion,
  Card,
  Tab,
  Row,
  Col,
  ListGroup,
  Form,
} from "react-bootstrap";
import { useTranslation } from "react-i18next";
import { Add, Store, Edit, FileCopy as FileCopyIcon } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
// import Dropzone from "../dropzone";
import PurposeRegistration from "../purposeRegistration";
import ShareholderRegistration from "../shareholderRegistration";
import Shareholder from "../shareholder";
import Pagination from "../pagination";
import Search from "../search";
import CopyableField from "../copyableField";
import AddBusiness from "./AddBusiness";
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "MyBusiness" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    userBusinesses,
    countBusinesses,
    businessesPerPage,
    activePageBusinesses,
    userBusinessesLoaded,
    activeBusiness,
    activeBusinessID,
    businessPurposes,
    countBusinessPurposes,
    businessPurposesPerPage,
    activePageBusinessPurposes,
    activeBusinesPurpose,
    activePurposeProperty,
    purposeShareholders,
    // purposeID,
    activePurposeData,
    // language,
    // accountNumber,
    userData,
  } = props.barrelGetters;
  const {
    setRedirectTo,
    setResponseProps,
    setBusinessPurposes,
    setActiveBusiness,
    setActiveBusinesPurpose,
    setActivePurposeProperty,
    setActivePageBusinessPurposes,
    setActivePurposeData,
    setPurposeRegistrationTitle,
    setPurposeRegistrationDescription,
    setPurposeRegistrationKeywords,
    setClearRefs,
  } = props.barrelSetters;
  const {
    getBusinesses,
    getBusinessPurposes,
    getPurposeShareholders,
    sendRequest,
    showRequestStatus,
    formatDate,
    copyToClipboard,
    findPurposes,
  } = props.barrelFunctions;
  // const {  } = props.barrelEventHandlers;

  /**
   * Module constants
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);
  const refBusinessID = useRef(null);
  const refPurposeID = useRef(null);

  /**
   * Functions
   ============================================================================ */

  /**
   * Toggle business (Accordion card)
   * @param {*} param0
   */
  const ToggleBusiness = ({ eventKey, businessID }) => {
    // console.log(activeBusiness, "===", eventKey, businessID);
    if (activeBusiness === eventKey) {
      setActiveBusiness(null);
      setActivePageBusinessPurposes(null);
    } else {
      // setActiveBusiness(eventKey);
      setResponseProps(null);
      getBusinessPurposes(businessID, eventKey, 1);
    }
  };

  /**
   * Toggle purpose (Accordion card)
   * @param {*} param0
   */
  const TogglePurpose = ({ eventKey, purposeID }) => {
    setResponseProps(null);
    getPurposeShareholders(purposeID, eventKey);
  };

  /**
   * Create:
   * - business menu
   * - business purposes menu
   */
  let menuItems = [];
  let purposes = [];
  let cntBusinesses = 0;
  let cntPurposes = 0;
  let countShareholders = 0;
  userBusinesses !== null &&
    Object.keys(userBusinesses).map((business, keyBusiness) => {
      cntBusinesses++;
      menuItems[keyBusiness] = [];
      purposes[keyBusiness] = [];
      businessPurposes !== null &&
        Object.keys(businessPurposes).map((purpose, keyPurpose) => {
          cntPurposes++;
          if (
            businessPurposes[purpose].business_id ===
            userBusinesses[business].business_id
          ) {
            menuItems[keyBusiness][keyPurpose] = (
              <ListGroup.Item
                action
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
                style={{
                  cursor: "default",
                  // whiteSpace: "nowrap",
                  overflow: "auto",
                }}
                onClick={() =>
                  TogglePurpose({
                    eventKey: keyPurpose,
                    purposeID: businessPurposes[purpose].purpose_id,
                  })
                }
              >
                <span style={{ cursor: "pointer" }}>
                  {businessPurposes[purpose].title}
                </span>

                <span style={{ paddingLeft: "5px", float: "right" }}>
                  <Edit
                    onClick={(e) => {
                      setPurposeRegistrationTitle(
                        businessPurposes[purpose].title !== null
                          ? businessPurposes[purpose].title
                          : ""
                      );
                      setPurposeRegistrationDescription(
                        businessPurposes[purpose].description !== null
                          ? businessPurposes[purpose].description
                          : ""
                      );
                      setPurposeRegistrationKeywords(
                        businessPurposes[purpose].keywords !== null
                          ? businessPurposes[purpose].keywords
                          : ""
                      );

                      setActivePurposeData(
                        JSON.stringify(businessPurposes[purpose])
                      );
                      setActiveBusinesPurpose("addPurpose");
                      e.stopPropagation();
                    }}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              </ListGroup.Item>
            );

            purposes[keyBusiness][keyPurpose] = (
              <Tab.Pane
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
              >
                <Accordion
                  defaultActiveKey={
                    activePurposeProperty !== null
                      ? activePurposeProperty
                      : "purpose_id" // "shareholders"
                  }
                >
                  {/* Purpose ID */}
                  <Card
                    style={{
                      border: "0",
                      borderRadius: "0",
                    }}
                  >
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "pointer",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="purpose_id"
                      onClick={() => setActivePurposeProperty("purpose_id")}
                    >
                      {t("Purpose ID")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="purpose_id">
                      <Card.Body style={{ background: "var(--white)" }}>
                        <Card.Text>
                          <input
                            ref={refPurposeID}
                            className="no-focus"
                            readOnly
                            style={{
                              border: 0,
                              width: "15rem",
                              background: "Transparent",
                            }}
                            type="text"
                            defaultValue={businessPurposes[purpose].purpose_id}
                          />
                          {document.queryCommandSupported("copy") && (
                            <span
                              style={{ float: "right", marginLeft: "0.25rem" }}
                            >
                              <button
                                className="button-transparent"
                                onClick={() => copyToClipboard(refPurposeID)}
                              >
                                <FileCopyIcon
                                  style={{ color: "var(--dark)" }}
                                />
                              </button>
                            </span>
                          )}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose shareholders */}
                  <Card style={{ border: "0", borderRadius: "0" }}>
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "default",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="shareholders"
                      onClick={() => setActivePurposeProperty("shareholders")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Shareholders")}
                      </span>
                      <span style={{ float: "right" }}>
                        <Add
                          onClick={(e) => {
                            setActivePurposeProperty("addShareholder");
                            e.stopPropagation();
                          }}
                          fontSize="small"
                          style={{ color: "var(--dark)", cursor: "pointer" }}
                        />
                      </span>
                    </Accordion.Toggle>

                    {/* Toggle: Add shareholder */}
                    <Accordion.Collapse eventKey="addShareholder">
                      <Card.Body>
                        <ShareholderRegistration
                          {...args}
                          purposeID={businessPurposes[purpose].purpose_id}
                        />
                      </Card.Body>
                    </Accordion.Collapse>

                    {/* Toggle: Shareholders list */}
                    <Accordion.Collapse eventKey="shareholders">
                      <Card.Body style={{ background: "var(--white)" }}>
                        {typeof purposeShareholders !== "undefined" &&
                          purposeShareholders !== null &&
                          Object.keys(purposeShareholders).map(
                            (shareholder, keyShareholder) => {
                              if (
                                purposeShareholders[shareholder].purpose_id ===
                                businessPurposes[purpose].purpose_id
                              ) {
                                const { purpose_id } = purposeShareholders[
                                  shareholder
                                ];
                                const {
                                  shareholder_id,
                                  firstname,
                                  lastname,
                                  // email,
                                  roles,
                                  share,
                                } = purposeShareholders[shareholder];

                                countShareholders++;

                                const arrRoles =
                                  roles !== null ? roles.split(",") : [];

                                return (
                                  <Shareholder
                                    {...args}
                                    key={keyShareholder}
                                    id={keyShareholder}
                                    shareholder={shareholder_id}
                                    purpose={purpose_id}
                                    firstname={firstname}
                                    lastname={lastname}
                                    share={share}
                                    roles={arrRoles}
                                  />
                                );
                              } else {
                                return ""; // TODO: check if this works
                              }
                            }
                          )}

                        {showRequestStatus(
                          "get purpose shareholders / SELECT shares",
                          countShareholders,
                          t("Loading shareholders..."),
                          t("No shareholders"),
                          "finished"
                        )}

                        {/* End shareholders list */}
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose status */}
                  <Card style={{ border: "0", borderRadius: "0" }}>
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "pointer",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="productStatus"
                      onClick={() => setActivePurposeProperty("productStatus")}
                    >
                      {t("Purpose Status")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="productStatus">
                      <Card.Body
                        style={{ background: "var(--white)" }}
                        onClick={(e) => {
                          // console.log("Click", e);

                          const bp = businessPurposes;
                          bp[purpose].status =
                            businessPurposes[purpose].status === 0 ? 1 : 0;
                          setBusinessPurposes(JSON.stringify(bp));

                          let data = {};
                          data.purpose_id =
                            businessPurposes[purpose].purpose_id;
                          data.purpose_status =
                            businessPurposes[purpose].status;
                          return sendRequest({
                            req: "change purpose status",
                            data,
                          });
                        }}
                      >
                        <Form>
                          {typeof purposeShareholders === "undefined" ||
                            (purposeShareholders === null && (
                              <Form.Text>
                                {t(
                                  "To publish, please add at least one shareholder"
                                )}
                                <br />
                                <br />
                              </Form.Text>
                            ))}

                          {typeof purposeShareholders !== "undefined" &&
                            purposeShareholders !== null && (
                              <Form.Check
                                type="switch"
                                custom
                                disabled={
                                  typeof purposeShareholders === "undefined" ||
                                  purposeShareholders === null
                                }
                                label={t(
                                  businessPurposes[purpose].status === 0
                                    ? "Not published"
                                    : "Published"
                                )}
                                checked={businessPurposes[purpose].status === 1}
                                onChange={() => {}}
                              />
                            )}
                        </Form>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose title */}
                  <Card style={{ border: "0", borderRadius: "0" }}>
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "default",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="title"
                      onClick={() => setActivePurposeProperty("title")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Title")}
                      </span>
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="title">
                      <Card.Body style={{ background: "var(--white)" }}>
                        <Card.Text>{businessPurposes[purpose].title}</Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/*
                   * Purpose description
                   */}
                  <Card style={{ border: "0", borderRadius: "0" }}>
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "default",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="description"
                      onClick={() => setActivePurposeProperty("description")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Description")}
                      </span>
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="description">
                      <Card.Body style={{ background: "var(--white)" }}>
                        <Card.Text>
                          {businessPurposes[purpose].description}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/*
                   * Keywords
                   */}
                  <Card style={{ border: "0", borderRadius: "0" }}>
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{
                        cursor: "default",
                        backgroundColor: "var(--light)",
                      }}
                      eventKey="keywords"
                      onClick={() => setActivePurposeProperty("keywords")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Keywords")}
                      </span>
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="keywords">
                      <Card.Body style={{ background: "var(--white)" }}>
                        <Card.Text>
                          {businessPurposes[purpose].keywords}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* End of purpose properties */}
                </Accordion>
              </Tab.Pane>
            );
          }
          countShareholders = 0;
          return true;
        });

      return true;
    });

  const jsx = (
    <>
      <Accordion
        defaultActiveKey={() => {
          if (userBusinesses === null) {
            return "addBusiness";
          }
          return activeBusiness !== null || !userBusinessesLoaded
            ? activeBusiness
            : "myBusinesses";
        }}
      >
        {/* Add business location top */}

        {/**
         * Card: Add business
         */}
        <AddBusiness {...args} />

        {/*
         * List user businesses
         */}
        {userBusinesses !== null &&
          Object.keys(userBusinesses).map((business, keyBusiness) => {
            const date = new Date(userBusinesses[business].created);
            const businessSince = formatDate(date, {
              month: "long",
              day: "numeric",
              year: "numeric",
            });

            return (
              <Card key={keyBusiness} style={{ borderRadius: "0" }}>
                <Card.Header style={{ fontWeight: "bold" }}>
                  <Store style={{ color: "var(--dark)" }} />{" "}
                  {userBusinesses[business].title}
                </Card.Header>
                <Card.Body>
                  <Tab.Container
                    id="left-tabs-example"
                    defaultActiveKey={
                      activeBusinesPurpose !== null
                        ? activeBusinesPurpose
                        : "aboutBusiness"
                    }
                  >
                    {/**
                     * Row: Search
                     */}
                    <Row style={{ marginBottom: "1.2rem" }}>
                      <Col>
                        <Search
                          advancedSearch={false}
                          placeholder={t("Find purposes...")}
                          captionAdvancedSearch={t("Advanced search")}
                          onSubmit={findPurposes}
                          {...args}
                        />
                      </Col>
                    </Row>

                    {/**
                     * Row: Purposes
                     */}
                    <Row>
                      {/**
                       * Layout: Left side - Menu
                       */}
                      <Col sm={5}>
                        <ListGroup variant="flush">
                          {/* Menu item: About business */}
                          <ListGroup.Item
                            action
                            eventKey="aboutBusiness"
                            style={{ cursor: "default" }}
                            onClick={(e) => {
                              setActivePurposeData(null);
                              setActiveBusinesPurpose(null);
                              e.stopPropagation();
                            }}
                          >
                            <span
                              style={{
                                whiteSpace: "nowrap",
                                cursor: "pointer",
                              }}
                            >
                              {t("Purposes")}
                            </span>
                            <span style={{ float: "right" }}>
                              <Add
                                onClick={(e) => {
                                  setClearRefs(true);
                                  setActivePurposeData(null);
                                  setActiveBusinesPurpose("addPurpose");
                                  e.stopPropagation();
                                }}
                                fontSize="small"
                                style={{
                                  color: "var(--dark)",
                                  cursor: "pointer",
                                }}
                              />
                            </span>
                          </ListGroup.Item>

                          {/* Menu items: Registered purposes */}
                          {menuItems[keyBusiness]}

                          {/* old add purpose */}
                        </ListGroup>
                        {countBusinessPurposes !== null && (
                          <>
                            <br />
                            <Pagination
                              {...args}
                              size="sm"
                              countItems={countBusinessPurposes}
                              itemsPerPage={businessPurposesPerPage}
                              active={activePageBusinessPurposes}
                              onClick={(number) =>
                                getBusinessPurposes(activeBusinessID, 0, number)
                              }
                            />
                          </>
                        )}

                        {showRequestStatus(
                          "get business purposes / SELECT purposes",
                          cntPurposes,
                          t("Loading purposes..."),
                          t("No purposes"),
                          "finished"
                        )}
                      </Col>

                      {/**
                       * Layout: Right side - Content
                       */}
                      <Col sm={7}>
                        <Tab.Content>
                          <Tab.Pane eventKey="aboutBusiness">
                            <Accordion defaultActiveKey="businessID">
                              {/* Business ID */}
                              <Card
                                style={{
                                  border: "0",
                                  borderRadius: "0",
                                  // backgroundColor: "red",
                                }}
                              >
                                <Accordion.Toggle
                                  as={Card.Header}
                                  style={{ cursor: "pointer" }}
                                  eventKey="businessID"
                                >
                                  {t("Business ID")}
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="businessID">
                                  <Card
                                    border="light"
                                    style={{ borderRadius: "0" }}
                                  >
                                    <Card.Body>
                                      <Card.Text>
                                        <CopyableField
                                          value={
                                            userBusinesses[business].business_id
                                          }
                                          width="12rem"
                                          {...args}
                                        />
                                      </Card.Text>
                                    </Card.Body>
                                  </Card>
                                </Accordion.Collapse>
                              </Card>

                              {/* Business description */}
                              <Card
                                style={{
                                  border: "0",
                                  borderRadius: "0",
                                  // backgroundColor: "red",
                                }}
                              >
                                <Accordion.Toggle
                                  as={Card.Header}
                                  style={{ cursor: "pointer" }}
                                  eventKey="businessDescription"
                                >
                                  {t("Business Description")}
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="businessDescription">
                                  <Card
                                    border="light"
                                    style={{ borderRadius: "0" }}
                                  >
                                    <Card.Body>
                                      <Card.Text>
                                        {userBusinesses[business]
                                          .description !== "" ? (
                                          userBusinesses[business].description
                                        ) : (
                                          <span className="text-center text-muted">
                                            {t("No description")}
                                          </span>
                                        )}
                                      </Card.Text>
                                    </Card.Body>
                                  </Card>
                                </Accordion.Collapse>
                              </Card>

                              {/* Business Founded */}
                              <Card
                                style={{
                                  border: "0",
                                  borderRadius: "0",
                                  // backgroundColor: "red",
                                }}
                              >
                                <Accordion.Toggle
                                  as={Card.Header}
                                  style={{ cursor: "pointer" }}
                                  eventKey="businessCreationDate"
                                >
                                  {t("Business Created")}
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="businessCreationDate">
                                  <Card
                                    border="light"
                                    style={{ borderRadius: "0" }}
                                  >
                                    <Card.Body>
                                      <Card.Text>{businessSince}</Card.Text>
                                    </Card.Body>
                                  </Card>
                                </Accordion.Collapse>
                              </Card>
                            </Accordion>
                          </Tab.Pane>

                          {/* Start Business purposes */}
                          {purposes[keyBusiness]}
                          {/* End Business purposes */}

                          {/*
                           * Add new purpose / update existing purpose
                           */}
                          <Tab.Pane eventKey="addPurpose">
                            <Accordion defaultActiveKey="newPurpose">
                              <Card style={{ border: "0", borderRadius: "0" }}>
                                <Accordion.Toggle
                                  as={Card.Header}
                                  eventKey="newPurpose"
                                  style={{ cursor: "default" }}
                                >
                                  {t(
                                    activePurposeData === null
                                      ? "New Purpose"
                                      : "Edit Purpose"
                                  )}
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="newPurpose">
                                  <Card.Body>
                                    <PurposeRegistration
                                      {...args}
                                      businessID={
                                        userBusinesses[business].business_id
                                      }
                                      id={
                                        activePurposeData !== null
                                          ? activePurposeData.id
                                          : null
                                      }
                                      purposeID={
                                        activePurposeData !== null
                                          ? activePurposeData.purpose_id
                                          : null
                                      }
                                      title={
                                        activePurposeData !== null
                                          ? activePurposeData.title
                                          : ""
                                      }
                                      description={
                                        activePurposeData !== null
                                          ? activePurposeData.description
                                          : ""
                                      }
                                      category={
                                        activePurposeData !== null
                                          ? activePurposeData.category
                                          : ""
                                      }
                                      subcategory={
                                        activePurposeData !== null
                                          ? activePurposeData.subcategory
                                          : ""
                                      }
                                      subcategory2={
                                        activePurposeData !== null
                                          ? activePurposeData.subcategory2
                                          : ""
                                      }
                                      keywords={
                                        activePurposeData !== null
                                          ? activePurposeData.keywords
                                          : ""
                                      }
                                    />
                                  </Card.Body>
                                </Accordion.Collapse>
                              </Card>
                            </Accordion>
                          </Tab.Pane>
                        </Tab.Content>
                      </Col>
                    </Row>
                  </Tab.Container>
                  {/* pagination prev */}
                </Card.Body>
              </Card>
            );
            // console.log("BUSINESS:", userBusinesses[business]);
          })}

        {/* Add business location bottom */}
      </Accordion>
      {userBusinesses !== null && (
        <>
          <br />

          <Pagination
            {...args}
            size="md"
            countItems={countBusinesses}
            itemsPerPage={businessesPerPage}
            active={activePageBusinesses}
            onClick={(number) => getBusinesses(number)}
          />
        </>
      )}
    </>
  );

  if (userData.uid === null) setRedirectTo("/sign-out");

  /**
   * Return
   ============================================================================ */
  return <>{jsx}</>;
};
