/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Accordion, Card } from "react-bootstrap";
import { Add, Store, Edit, FileCopy as FileCopyIcon } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import BusinessRegistration from "../businessRegistration";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "MyBusiness" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { activeBusiness } = props.barrelGetters;
  const { setActiveBusiness } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  return (
    <>
      <Card style={{ background: "Transparent", border: "0" }}>
        <Accordion.Toggle
          as={Card.Header}
          eventKey="addBusiness"
          style={{
            background: "Transparent",
            padding: "0 0 5px 0",
          }}
        >
          <span
            style={{
              float: "right",
              cursor: "pointer",
              // backgroundColor: "#fff",
            }}
            onClick={() =>
              setActiveBusiness(
                activeBusiness !== "addBusiness" ? "addBusiness" : null
              )
            }
          >
            {t("Add Business")}
            &nbsp;
            <Add
              style={{
                color: "var(--dark)",
                fontSize: "1.2rem",
                paddingBottom: "0.2rem",
              }}
            />
          </span>
        </Accordion.Toggle>
        <Accordion.Collapse eventKey="addBusiness">
          <Card.Body>
            <BusinessRegistration {...args} />
          </Card.Body>
        </Accordion.Collapse>
      </Card>
    </>
  );
};
