/**
 * CSS
 ============================================================================ */
import "./LinkList.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import InjectChildren from "../injectChildren";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "LinkList",
    title: "Untitled",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  return (
    <div className="link-list">
      <h4>{props.title}</h4>
      <InjectChildren {...args} />
    </div>
  );
};
